// 
import clickoutside from './extend/clickoutside/index.js';
import request from './request/request.js';
import autocomplete from './extend/autocomplete.js';
import searchList from './extend/search-list.js';
// module
export default {
    // 
    mixins: [request, autocomplete, searchList],
    // 
    //  p  private  
    directives: {pclickoutside: clickoutside},
    // 
    model: {
        prop: 'value',
        event: 'input'
    },
    props: {

        // 
        value: {
            type: [String, Number],
            default: ''
        },

        // 
        dName: {
            type: String,
            default: 'text'
        },
        //  
        dataList: {
            // 
            type: Array,
            //  [String,Number]
            default: function () {
                return [];
            }
        },

        // 
        disabled: {
            type: Boolean,
            default: false
        },


        // 
        clearable: {
            type: Boolean,
            default: true
        },

        // 
        placeholder: {
            type: String,
            default: ''
        },

        /*     body 
         *      Tabs fixed  Table 
         */
        transfer: {
            type: Boolean,
            default: false
        },

        // 
        remoteMethod: {
            // 
            type: String,
            default: 'get'
        },

        // url
        remoteUrl: {
            type: String,
            default: './static/json/auto-complete/dataList.json'
        },

        // 
        remoteExtendsParams: {type: Object},

        // 
        remoteRequestMethod: {type: Function}
    },
    data() {
        return {
            // 
            scrollBar: null,
            // 
            currentItem: {},
            // 
            currentValue: this.value
        };
    },
    computed: {},
    watch: {
        value: {
            handler(val) {
                // 
                this.setCurrentValue(val);
            },
            immediate: true
        }
    },
    mounted() {
        // ready
        this.$emit('ready', this);
    },
    methods: {
        // 
        setCurrentValue(value) {
            this.currentValue = value;
        },

        // 
        clickClear: function () {
            // 
            this.resetCurrentItem();
            // 
            this.emitEvent();
        },

        // 
        resetCurrentItem() {
            // 
            this.setCurrentItem({});
        },
        // 
        setCurrentItem(item) {
            // 
            this.currentItem = item;
            // 
            this.setCurrentValue(this.currentItem[this.dName]);
            // 
            this.emitEvent();
        },
        // 
        emitEvent() {
            // select 
            this.$emit('select', this.currentItem, this);
        },

        //  
        remoteSearch: function (query) {
            // 
            this.setRemoting(true);
            // 
            if (typeof this.remoteRequestMethod === 'function') {
                this.remoteRequestMethod(query).then((dataList) => {
                    // 
                    this.slOperateData(dataList);
                })
                    .finally(() => {
                        // 
                        this.setRemoting(false);
                    });
            } else {
                // 
                this.requestRemoteData(query).then((dataList) => {
                    // 
                    this.slOperateData(dataList);
                })
                    .finally(() => {
                        // 
                        this.setRemoting(false);
                    });
            }
        },
        // 
        requestRemoteData(query) {
            return new Promise((resolve, reject) => {
                // url(url)
                if (this.remoteUrl) {
                    // ,  
                    const _data = {};
                    // 
                    if (Object.prototype.toString.call(this.remoteExtendsParams) === '[object Object]') {
                        for (const _name in this.remoteExtendsParams) {
                            _data[_name] = this.remoteExtendsParams[_name];
                        }
                    }
                    // 
                    _data.query = query;
                    // ajax 
                    const _ajaxOptions = {
                        method: this.remoteMethod,
                        data: _data,
                        url: this.remoteUrl
                    };
                    // 
                    this.requestDataList(_ajaxOptions, '').then((dataList) => {
                        resolve(dataList);
                    }, (data) => {
                        reject(data);
                    });
                } else {
                    reject(new Error('remoteUrl'));
                }
            });
        }
    }
};



