/**
 * @version 20190401
 * @author wuwg
 * @createTime  20190416
 * @updateTime  20190422
 * @decription  
 */
export default {
    methods: {
        // 
        edDropDragDataItem(parentItem, dataItem, index) {
            // 
            const _index = this.edDropIsPrev ? index : index + 1;
            // 
            parentItem.children.splice(_index, 0, this.edCurrentDragDataItem);
            // 
            this.currentDataList = JSON.parse(JSON.stringify(this.edCopyCurrentDataList));
        },

        // 
        edDeleteDragDataItem(dragItemP, dragItem) {
            let _dataList = this.dataList;
            if (dragItemP) {
                _dataList = dragItemP[this.dChildren];
            }
            // 
            _dataList.every((item, index) => {
                if (item[this.dKey] === dragItem[this.dKey]) {
                    // 
                    _dataList.splice(index, 1);
                    return false;
                }
                return true;
            });
        },

        /**
         * @description 
         * @param {object} dragItem 
         * @param {object} dragItemNewP 
         * @returns {*} 
         */
        edAddDragDataInner(dragItem, dragItemNewP) {
            // id
            dragItem[this.dpKey] = dragItemNewP[this.dKey];
            // 
            dragItemNewP[this.dChildren].unshift(dragItem);
            // 
            dragItemNewP.extend = true;
        },

        /**
         * @description 
         * @param {object} dragItem 
         * @param {object} dragItemNewP 
         * @param {object} dropItem 
         * @param {string}edDropPostion top|bottom
         * @returns {*} 
         */
        edAddDragData(dragItem, dragItemNewP, dropItem, edDropPostion) {
            let _dataList = this.dataList;
            // 
            if (dragItemNewP) {
                _dataList = dragItemNewP[this.dChildren];
                // id
                dragItem[this.dpKey] = dragItemNewP[this.dKey];
                // 
                dragItemNewP.extend = true;
            } else {
                // id
                dragItem[this.dpKey] = '-1';
            }
            // 
            _dataList.every((item, index) => {
                // 
                if (item[this.dKey] === dropItem[this.dKey]) {
                    // 
                    const _index = edDropPostion === 'top' ? index : index + 1;
                    // 
                    _dataList.splice(_index, 0, dragItem);
                    return false;
                }
                return true;
            });
        },

        /**
         * @description 
         * @returns {*} 
         */
        edOperateOriginDataList() {
            // 
            // important 
            // this.edCurrentDragDataItem  this.edCurrentDropDataItem copy
            // 
            // id
            const _edCurrentDragDataItem = this.cGetCache(this.edCurrentDragDataItem[this.dKey]);
            if (_edCurrentDragDataItem) {
                // 
                const _edCurrentDropDataItem = this.cGetCache(this.edCurrentDropDataItem[this.dKey]);
                const _edDropPostion = this.edDropPostion;
                // 1.
                this.gSetFocusItem(_edCurrentDragDataItem);
                // 2.
                this.dDeleteTreeNode(_edCurrentDragDataItem);
                // 3.
                this.dInsertTreeNode(_edCurrentDragDataItem, _edCurrentDropDataItem, _edDropPostion, true);
            }
        }
    }
};




