import request from './request/request.js';
const NUM_25 = 25;
// 
export default {
    name: 'test3',
    // 
    mixins:[request],
    props: {test: {type: null}},
    data() {
        return {
            message: 'test3',
            showLog: window.fdConfig.showLog,
            serverUrl: window.fdConfig.url.test.index,
            data: []
        };
    },
    methods: {
        // 
        clickGetData() {
            return new Promise((resolve) => {
                // 
                const queryData = {
                    query: '',
                    age: NUM_25
                };
                // 
                this.requestUserData(queryData).then((data) => {
                    this.data = data;
                    resolve();
                });
            });
        }
    },
    mounted() {
        //  console.log(this)
    }
};



