import Vue from 'vue';
// eventBus
const globalBus = new Vue({
    data() {
        return {
            commonActiveNodeInfo: {}
            // 
        };
    },
    created() {
        // 
        this.bindGlobalEvent();
    },
    beforeDestroy() {
        // 
        this.unbindGlobalEvent();
    },
    methods: {
        // 
        setCommonActiveNodeInfo(event, data) {
            // debugger;
            this.commonActiveNodeInfo = data;
        },
        // 
        bindGlobalEvent() {
            // 
            this.$on('global-common-activeNodeInfo', this.setCommonActiveNodeInfo);
        },
        // 
        unbindGlobalEvent() {
            // 
            this.$off('global-common-activeNodeInfo', this.setCommonActiveNodeInfo);
        }
    }
});
export default globalBus;



