import mockData from './mockData.js';
// 
const module = {
    props: {
        // 
        dataObject: {
            type: Object,
            // 
            validator(dateItem) {
                //  dataTitle  data
                return dateItem.hasOwnProperty('category') && dateItem.category === 'form-table' && dateItem.hasOwnProperty('data');
            },
            // 
            default() {
                return mockData;
            }
        }
    },
    data() {
        return {dataList: []};
    },
    // 
    created() {
        this.init();
    },
    methods: {

        /**
         * @description 
         * @name init
         * @return {*} 
         */
        init() {
            this.update(this.dataObject);
        },
        // 
        update(dataObject) {
            // 
            this.dataList = dataObject.data;
        },
        //  
        clickOperate(itemCC, item) {
            window.console.log(itemCC, item);
        }
    }
};
export default module;



