export default {
    methods: {
        // 
        eCheckboxHandler(dataItem) {
            // ||
            dataItem.checked = !dataItem.checked;
            // 
            this.eCheckboxHandleCallback(dataItem);
        },
        // 
        eCheckboxHandleCallback(dataItem, triggerEvent = true) {
            const _this = this;
            //  
            this.cUpdateCache(dataItem, 'checked');
            //  
            if (this.connectChecked) {
                // 1. 
                this.eCheckboxHandleConnectCallback(dataItem, dataItem.checked);
                // 2. 
                this.eUpdateView();
            }
            //  
            if (triggerEvent) {
                // 
                this.emitEvent('checked', null, dataItem, _this);
            }
        },
        // 
        eCheckboxHandleConnectCallback(dataItem, checked) {
            // 
            const _targetItem = this.cGetCache(dataItem[this.dKey]);
            // 
            // 
            if (this.connectParent) {
                // 1.
                this.eCheckboxHandleConnectParentCallback(_targetItem, checked);
            }
            // 
            if (this.connectChildren) {
                // 2.
                this.eCheckboxHandleConnectChildrenCallback(_targetItem, checked);
            }
        },
        // 
        eCheckboxHandleConnectParentCallback(dataItem, checked) {
            // 
            const _pDataItem = this.cGetCache(dataItem[this.dpKey]);
            // 
            if (_pDataItem) {
                // 1.   
                if (checked && (!_pDataItem.checked)) {
                    // 
                    const _checked = _pDataItem[this.dChildren].every((item) => item.checked);
                    // 1.1 
                    _pDataItem.checked = _checked;
                    // 1.2.  ,
                    // 1.2. 
                    if (_checked) {
                        // 
                        this.cUpdateCache(_pDataItem, 'checked');
                        // 
                        this.eCheckboxHandleConnectParentCallback(_pDataItem, checked);
                    }
                }
                // 2.   
                // 
                if (!checked && _pDataItem.checked) {
                    // 2.1 
                    _pDataItem.checked = false;
                    // 
                    this.cUpdateCache(_pDataItem, 'checked');
                    // 2.2 
                    this.eCheckboxHandleConnectParentCallback(_pDataItem, checked);
                }
            }
        },
        // 
        eCheckboxHandleConnectChildrenCallback(dataItem, checked) {
            // 
            if (dataItem[this.dChildren].length) {
                // 1.  , 
                // 2.  , 
                this.eCheckboxRecursionChildren(dataItem[this.dChildren], checked);
            }
        },
        // 
        eCheckboxRecursionChildren(dataList, checked) {
            // 
            dataList.forEach((item) => {
                // 
                if (item.checked !== checked) {
                    // 1. 
                    item.checked = checked;
                    // 
                    this.cUpdateCache(item, 'checked');
                    // 2. ,
                    if (item[this.dChildren].length) {
                        // 
                        this.eCheckboxRecursionChildren(item[this.dChildren], checked);
                    }
                }
            });
        }
    }
};



