// 
// import scrollbar from '../scrollbar/index.vue';
// 
import type from './utils/tools/type.js';
// 
import performance from './utils/tools/performance.js';
// 
// import isEmptyDirectory from './js/yw/isEmptyDirectory.js';
// 
// import yAddProperty from './js/yAddProperty.js';
// 
// import ywIndex from './js/yw/index.js';
// 
// import makeMaterialList from './js/yw/makeMaterialList.js';
// 
import makeOneDimensionalList from './js/makeOneDimensionalList.js';

/**
 *      
 **********************/
// 
// import treeList from './mock/treeList.js';
// 
import dOperateTreeData from './js/dOperateTreeData.js';
// 
import cache from './js/performance/cache.js';
// 
import globalMethods from './js/globalMethods.js';
// 
import calc from './js/cacl/calc.js';
// 
import emitEvent from './js/emitEvent.js';


/**
 *      
 **********************/
// 
import computedHeight from './js/performance/computedHeight.js';
// 
import style from './js/style.js';
// 
import bindEvent from './js/bindEvent.js';

// 
export default {
    name: 'vcc-tree',
    // components
    // components: {scrollbar},
    mixins: [
        dOperateTreeData,
        globalMethods,
        cache,
        computedHeight,
        style,
        bindEvent,
        calc,
        emitEvent
        // ywIndex
    ],
    props: {

        // id
        id: {
            type: String,
            default: function () {
                return `jsTreeId${(Math.random() * Math.pow(10, 6)).toFixed(0)}`; // eslint-disable-line
            }
        },
        // 
        name: {
            type: String,
            default: ''
        },

        // keykey
        dpKey: {
            type: String,
            default: 'pid'
        },

        // key
        dKey: {
            type: String,
            default: 'id'
        },
        // name
        dName: {
            type: String,
            default: 'name'
        },
        // 
        dChildren: {
            type: String,
            default: 'children'
        },
        // 
        customName: {type: Function},

        // 
        dCategory: {
            type: String,
            default: 'category'
        },
        // 
        divideCategory: {
            type: Boolean,
            default: false
        },

        /**
         * #property
         *
         * @name    dData
         * @cn      
         * @value   []
         * @description 
         */
        dData: {
            type: Array,
            default() {
                // return treeList;
                return [];
            }
        },

        /**
         * #property
         *
         * @name    hasLine
         * @cn      
         * @group   
         * @type    boolean
         * @value   false
         * @description ,true
         */
        hasLine: {
            type: Boolean,
            default: true
        },
        // 
        itemHeight: {
            type: Number,
            default: 30
        },
        //   !important
        clientCount: {
            type: Number,
            default: 30
        },
        // 
        // 
        prepareCount: {
            type: Number,
            default: 10
        },

        /**
         * #property
         *
         * @name    hasIcon
         * @cn      
         * @group   
         * @type    boolean
         * @value   false
         * @description ,false
         */
        hasIcon: {
            type: Boolean,
            default: false
        },

        // 
        longTapTime: {
            type: Number,
            default: 300
        },
        // 
        multiple: {
            type: Boolean,
            default: false
        },
        // 
        connectChecked: {
            type: Boolean,
            default: true
        },
        // 
        connectParent: {
            type: Boolean,
            default: true
        },
        // 
        connectChildren: {
            type: Boolean,
            default: true
        },

        /**
         * #property
         *
         * @name    canMendName
         * @cn      
         * @type    boolean
         * @value   false
         * @description ,false
         */
        canMendName: {
            type: Boolean,
            default: false
        },
        // 
        keepNameSuffix: {
            type: Boolean,
            default: false
        },

        // 
        canDisabled: {
            type: Boolean,
            default: false
        },

        // boolean
        edCanMendNameCallback: {
            type: Function,
            default() {
                return true;
            }
        },

        /**
         * #property
         *
         * @name    edPreviewId
         * @cn      
         * @description ,#jsTreePreview
         */
        edPreviewId: {
            type: String,
            default: '#jsTreeDragContent'
        },

        /**
         * #property
         *
         * @name    edDraggable
         * @cn      
         * @type    boolean
         * @value   false
         * @description ,false
         */
        edDraggable: {
            type: Boolean,
            default: false
        },

        // boolean
        edCanDragCallback: {
            type: Function,
            default() {
                return true;
            }
        },
        // boolean
        edCanDropCallback: {
            type: Function,
            default() {
                return true;
            }
        },
        // 
        edCanDropPosition: {
            type: Function,
            default(dropItem, dragItem, event, vm) { // eslint-disable-line
                return ['top', 'middle', 'bottom'];
            }
        },
        // 
        edSetDragPreviewHtmlCallback: {
            type: Function,
            default(dragItem, vm) {
                // 
                if (dragItem.type !== 'material') {
                    return [
                        `<div class="text  icon ${dragItem.type}">`,
                        dragItem[vm.dName] ? `${dragItem[vm.dName]}` : '',
                        '</div>'
                    ].join('');
                }
                // 
                return [
                    `<div class="preview ${dragItem.type}">`,
                    '<img src="./static/img/cailiao-3.jpg">',
                    '</div>'
                ].join('');
            }
        },
        // promise
        edDropHandlerCallback: {
            type: Function,

            /**
             * @param {object}dragItem 
             * @param {object}dropItem 
             * @param {string}dropPostion 
             * @param {object} vm  
             * @returns {Promise} promise
             */
            default(dragItem, dropItem, dropPostion, vm) { // eslint-disable-line
                return new Promise((resolve, reject) => {
                    if (this.edDraggable) {
                        resolve();
                    } else {
                        reject(new Error(''));
                    }
                });
            }
        },
        // 
        eRenamePatternCallback: {
            type: Function,

            /**
             * @param {object}dataItem 
             * @param {object} vm  
             * @returns {boolean} boolean
             */
            default(dataItem, vm) { // eslint-disable-line
                let _value = dataItem[this.dName];
                // 
                _value = _value.replace(/(^\s+)|(\s+$)/igm, '');
                if (_value === '') {
                    return false;
                }
                // return /^[A-Za-z0-9-(-)-.-_\uff08-\uff09-\u4e00-\u9fa5-\u3001]+$/.test(value)  
                return !(/[\/\\:\*\?<>"\|]/img).test(_value); // eslint-disable-line
            }
        },
        // promise
        eRenameHandlerCallback: {
            type: Function,

            /**
             * @param {object}dataItem 
             * @param {object} vm  
             * @returns {Promise} promise
             */
            default(dataItem, vm) { // eslint-disable-line
                return new Promise((resolve, reject) => {
                    if (this.canMendName) {
                        resolve();
                    } else {
                        reject(new Error(''));
                    }
                });
            }
        },
        // promise
        dAddTreeNodeHandlerCallback: {
            type: Function,

            /**
             * @param {object}dataItem 
             * @param {object} vm  
             * @returns {Promise} promise
             */
            default(dataItem, vm) {
                return new Promise((resolve) => {
                    const _count = 2;
                    const _newNode = JSON.parse(JSON.stringify(dataItem));
                    const _position = ['top', 'middle', 'bottom'][Math.round(Math.random() * _count)];
                    const _data = {};
                    // id,id
                    _newNode[vm.dKey] = `${_newNode[vm.dKey]}new${Math.round(Math.random() * Math.pow(10, 6))}`;
                    // id
                    _newNode[vm.dpKey] = dataItem[vm.dpKey];
                    // 
                    _newNode[vm.dName] = '';
                    // 
                    _newNode[vm.dChildren] = [];
                    // 
                    if (_position === 'middle') {
                        _newNode[vm.dpKey] = dataItem[vm.dKey];
                    }
                    // 
                    _data.targetNode = dataItem;
                    _data.newNode = _newNode;
                    _data.position = _position;
                    _data.isBefore = true;
                    // 
                    resolve(_data);
                    // reject()
                });
            }
        },
        // promise
        dRemoveTreeNodeHandlerCallback: {
            type: Function,

            /**
             * @param {object}dataItem 
             * @param {object} vm  
             * @returns {Promise} promise
             */
            default(dataItem, vm) { // eslint-disable-line
                return new Promise((resolve) => {
                    // 
                    resolve();
                });
            }
        },
        // 
        dAddTreeNodeAfterCallback: {
            type: Function,

            /**
             * @param {object}dataItem 
             * @param {object} vm  
             * @returns {Promise} promise
             */
            default(dataItem, vm) {
                // 
                vm.currentDataList.every((item) => {
                    if (item[vm.dKey] === dataItem[vm.dKey]) {
                        // 
                        item.renaming = true;
                        vm.$nextTick(() => {
                            // 
                            vm.$refs[`jsInput${dataItem[vm.dKey]}`][0].focus();
                            // 
                            vm.$refs[`jsInput${dataItem[vm.dKey]}`][0].select();
                            // 
                            vm.$emit('focus', dataItem);
                        });
                        // 
                        return false;
                    }
                    return true;
                });
            }
        },
        // 
        dAddOtherPropertyCallback: {
            type: Function,

            /**
             * @param {object}dataItem 
             * @param {object}dataItemP 
             * @param {object} vm  
             * @returns {Promise} promise
             */
            default(dataItem, dataItemP, vm) { // eslint-disable-line
                // 
                if (dataItem && dataItem.details) {
                    // 
                    // @todo  2019-11-10  wuwg 
                    // yAddProperty.addOtherProperty(dataItem, vm);
                }
            }
        },
        // 
        dAddIconGroupCallback: {
            type: Function,
            default(dataItem, vm) { // eslint-disable-line
                // 
                // if (dataItem && dataItem.details) {
                //     // return  yAddProperty.setIcon(dataItem);
                //     return [];
                // }
                return [];
            }
        },
        // class
        dAddClassCallback: {
            type: Function,
            default(dataItem, dataItemP, vm) { // eslint-disable-line
                // 
                // if (dataItem && dataItem.details) {
                //     return yAddProperty.setClass(dataItem, dataItemP);
                // }
                return '';
            }
        },
        // 
        dMakeListCallback: {
            type: Function,
            default(dataItem, previewList) { // eslint-disable-line
                // previewList[]
                // if (dataItem.details) {
                //     makeMaterialList(dataItem, previewList);
                // }
            }
        },
        // 
        dIsEmptyFolderCallback: {
            type: Function,
            default(dataItem, vm) { // eslint-disable-line
                // previewList[]
                // if (dataItem.type === 'directory') {
                //     // 
                //     return isEmptyDirectory(dataItem, vm);
                // }
                return false;
            }
        },

        // 
        dIsFolderCallback: {
            type: Function,
            default(dataItem) {
                // previewList[]
                return dataItem.type === 'directory';
            }
        },


        // 
        dOneDimensionalCallback: {
            type: Function,
            default(dataItem, oneDimensionalList, vm) { // eslint-disable-line
                // 
                makeOneDimensionalList(dataItem, oneDimensionalList, vm);
            }
        },

        // indexName
        // name  420ms-50ms 
        addIndexName: {
            type: Boolean,
            default: false
        },
        // indexIdArrayid id - 
        addIndexIdArray: {
            type: Boolean,
            default: false
        },

        //  0
        // 
        startIndex: {
            type: Number,
            default: 0
        },

        // 
        showLevel: {
            type: Number,
            default: 1
        },

        // 
        iconWidth: {
            type: Number,
            default: 20
        },

        // 
        checkboxWidth: {
            type: Number,
            default: 24
        },

        // 
        responseType: {
            type: Boolean,
            default: false
        },

        //  
        eScrollDelay: {
            type: Number,
            default: 1000
        },
        //  
        //  treeDataList 
        isUniqueTreeData: {
            type: Boolean,
            default: true
        },
        //  
        //  treeDataList 

        //   true
        expandOnClickNode: {
            type: Boolean,
            default: true
        },

        //  false
        checkOnClickNode: {
            type: Boolean,
            default: false
        },

        // 
        asyncNodeData: {
            type: Boolean,
            default: false
        },

        // promise
        asyncNodeDataHandlerCallback: {
            type: Function,

            /**
             * @param {object}dataItem 
             * @param {object} vm  
             * @returns {Promise} promise,promise 
             */
            default(dataItem, vm) { // eslint-disable-line
                return new Promise((resolve) => {
                    const _dataList = [];
                    resolve(_dataList);
                });
            }
        },

        // ...
        gAdjustEllipsisHandler: {
            type: Function,
            default(dataItem, vm) { // eslint-disable-line
                return dataItem.name.length > 5; // eslint-disable-line
            }
        }

    },
    computed: {
        // 
        getStyle() {
            // 
            const _iconWidth = this.iconWidth;
            const _checkbox = this.checkboxWidth;
            let _marginLeft = this.multiple ? _checkbox : 0;
            _marginLeft = this.hasIcon ? _marginLeft + _iconWidth : _marginLeft;
            return {'margin-left': `${_marginLeft}px`};
        }
    },
    data() {
        return {
            // 
            currentDataList: [],
            // 
            lightList: null,

            /*  lightList: {
             "B05236DCD6DE479DAE4C2033D9E051E6":true
             }*/
            // 
            noLevel1: false,
            noLevel2: false,
            noLevel3: false,
            noLevel4: false,
            noLevel5: false,
            treeScrollbar: null
        };
    },
    beforeCreate() {
        // performance
        if (!window.fdGlobal) {
            window.fdGlobal = {performance: performance};
        }
        if (!window.fdGlobal.performance) {
            window.fdGlobal.performance = performance;
        }
    },
    // 
    created() {
        // 
        this.init();
    },
    //  dom 
    mounted() {
        // console.log(this.dData)
        // 
        this.emitEvent('ready');
        // mounted,
        this.eScroll();
    },
    methods: {
        // 
        init() {
            // 1.
            this.dInitVariable();
            // 2. 
            this.dpInitVariable();
            // 3. ,
            this.dTransformTreeData();
            // 4. 
            this.createWindowCacheObject();
            // 5. resize
            this.eBindResize();
        },

        /**
         * 
         * @param {object} treeScrollbar 
         * @returns {undefined}  
         */
        readyScrollbar(treeScrollbar) {
            this.treeScrollbar = treeScrollbar;
        },
        // 
        updateScrollbar() {
            this.$nextTick(() => {
                // 
                this.treeScrollbar.refresh(false);
            });
        },
        // 
        operateLightStatus(data) {
            if (this.lightList) {
                data.forEach((item) => {
                    // 
                    item.light = this.lightList[item[this.dKey]] === true;
                });
            }
        },
        // 
        getLightList() {
            return this.lightList;
        },
        // 
        setLightList(dataList) {
            if (Array.isArray(dataList)) {
                this.lightList = {};
                dataList.forEach((item, index) => { // eslint-disable-line
                    // 
                    this.lightList[item[this.dKey]] = true;
                });
            }
        },
        // 
        clearLightList() {
            this.lightList = null;
        },


        /**
         * @param {'array'} dataList 
         * @description currentDataList
         * @returns {*} 
         */
        setCurrentDataList(dataList) {
            if (Array.isArray(dataList)) {
                // 
                this.operateLightStatus(dataList);
                // 
                this.currentDataList = dataList;
            } else if (dataList === 'highlight') {
                const data = this.currentDataList;
                this.operateLightStatus(data);
                this.currentData = data;
            }
        },
        // 
        refresh(callback, isTurnPage = true) {
            // 
            this.eUpdateViewCount = 0;
            const _name = `${this.name}---refresh`;
            const _this = this;
            const _startTime = window.fdGlobal.performance.getCurrentTime();
            // 1.
            this.dTransformTreeData();
            window.fdGlobal.performance.execute(`${_name}1. :`, _startTime);
            // 2. 
            this.eUpdateView(() => {
                window.fdGlobal.performance.execute(`${_name}2. :`, _startTime);
                // 
                if (this.gActiveItem[this.dKey]) {
                    const _currentItem = this.cGetCache(this.gActiveItem[this.dKey]);
                    // 
                    if (_currentItem && isTurnPage) {
                        // 
                        this.caclSetcaclCurrentPagesList(_currentItem);
                    }
                }
                if (type(callback) === 'function') {
                    callback(_this);
                }
            });
            // 
            this.emitEvent('refresh');
        },
        // 
        refreshCurrentView(callback) {
            // 
            this.eUpdateView(callback);
        },
        // 
        update(dataList, callback, isTurnPage = true, isUniqueTreeData) {
            const _this = this;
            const _name = `${this.name}---update`;
            const _startTime = window.fdGlobal.performance.getCurrentTime();
            if (type(dataList) === 'array') {
                window.fdGlobal.performance.execute(`${_name}0 :`, _startTime);
                // 1. 
                this.dSetDataList(dataList, typeof isUniqueTreeData === 'boolean' ? isUniqueTreeData : this.isUniqueTreeData);
                window.fdGlobal.performance.execute(`${_name}1 dataList :`, _startTime);
                // 2. 
                this.dTransformTreeData();
                window.fdGlobal.performance.execute(`${_name}2 :`, _startTime);
                // 3. 
                this.eUpdateView(() => {
                    window.fdGlobal.performance.execute(`${_name}4 :`, _startTime);
                    // 
                    if (this.gActiveItem[this.dKey]) {
                        // 
                        const _currentItem = this.cGetCache(this.gActiveItem[this.dKey]);
                        // 
                        if (_currentItem && isTurnPage) {
                            // 
                            this.caclSetcaclCurrentPagesList(_currentItem);
                        }
                    }
                    window.fdGlobal.performance.execute(`${_name}5 :`, _startTime);
                    if (type(callback) === 'function') {
                        callback(_this);
                    }
                    window.fdGlobal.performance.execute(`${_name}6 :`, _startTime);
                });
                // 
                this.emitEvent('update');
                window.fdGlobal.performance.execute(`${_name}3 :`, _startTime);
            }
        },
        // 
        nextTick(callback) {
            const _this = this;
            if (type(callback) === 'function') {
                _this.$nextTick(() => {
                    callback(_this);
                });
            }
        }

    }
};



