const NUM_2000 = 2000;
export default {
    name: 'page-b',
    data() {
        return {name: '> B'};
    },
    methods: {
        clickAlertSimple() {
            this.$Alert.alert('');
        },
        clickAlert() {
            this.$Alert.alert({
                width: 300,
                height: 180,
                zIndex: 90,
                modal: true,
                title: '',
                content: '',
                confirm:{
                    show: true,
                    text: '',
                    callback() {
                        window.console.log('');
                    }
                }
            });
        },
        clickPrompt() {
            this.$Alert.prompt({
                width: 300,
                height: 200,
                zIndex: 90,
                modal: true,
                title: '',
                content: '',
                value: '',
                confirm:{
                    show: true,
                    text: '',
                    callback(value) {
                        window.console.log('');
                        window.console.log(`${value}`);
                    }
                },
                cancle:{
                    show: true,
                    text: '',
                    callback() {
                        window.console.log('');
                    }
                }
            });
        },
        clickConfirm() {
            this.$Alert.confirm({
                width: 300,
                height: 180,
                zIndex: 90,
                modal: true,
                title: 'confirm',
                content: '',
                confirm:{
                    show: true,
                    text: '',
                    callback() {
                        window.console.log('');
                    }
                },
                cancle:{
                    show: true,
                    text: '',
                    callback() {
                        window.console.log('');
                    }
                }
            });
        },
        clickInfo() {
            this.$Message.info('information');
        },
        clickSuccess() {
            this.$Message.success('success');
        },
        clickWarning() {
            this.$Message.warning('warning');
        },
        clickError() {
            this.$Message.error('error');
        },
        clickLoading() {
            this.$Message.loading('loading...');
        },

        clickAjaxStart() {
            this.$AjaxLoading.start();
            // 2
            setTimeout(() => {
                this.clickAjaxFinish();
            }, NUM_2000);
        },
        clickAjaxFinish() {
            this.$AjaxLoading.finish();
        }
    }
};




