/**
 *@author   wuwg
 *@createTime   2018/10/25 0012
 *@updateTime   2018/10/25 0012
 *@description  module  alert component
 */
import config from '@/components/config/component-config.js';
// prefix
const prefixCls = 'fd-alert';
// alert
const module = {
    name: `${config.componentCcPrefix}alert`,
    props: {
    // class
        className: String,
        // 
        animateName: {
            type: String,
            default: 'modal'
        },
        // 
        width: [Number, String],
        // 
        height: [Number, String],
        // z-index
        zIndex: [Number],
        // 
        modal: Boolean,
        // 
        type: {
            default: 'confirm',
            validator(value) {
                // 
                return ['alert', 'confirm', 'prompt'].indexOf(value) !== -1;
            }
        },
        // 
        title: {
            type: String,
            default: ''
        },
        // 
        content: {
            type: String,
            default: ''
        },
        // 
        value: {
            type: String,
            default: ''
        },
        // 
        confirm: {
            type: Object,
            default: function () {
                return {
                    show: true,
                    text: '',
                    callback() {
                        window.console.log('');
                    }
                };
            }
        },
        // 
        cancel: {
            type: Object,
            default: function () {
                return {
                    show: true,
                    text: '',
                    callback() {
                        window.console.log('');
                    }
                };
            }
        }
    },
    watch: { },
    data() {
        return {
            // 
            currentValue: this.value
        };
    },
    computed: {
    // class
        wrapClasses() {
            return [
                prefixCls,
                this.type,
                this.className,
                {opacity0: this.modal}
            ];
        },
        // class
        popupClasses() {
            return [
                `${prefixCls}-popup`
            ];
        },
        // class
        popupHeaderClasses() {
            return [
                `${prefixCls}-popup-header`
            ];
        },
        // body class
        popupBodyClasses() {
            return [
                `${prefixCls}-popup-body`
            ];
        },
        // class
        popupFooterClasses() {
            return [
                `${prefixCls}-popup-footer`
            ];
        },
        // 
        currentStyle() {
            const _style = {};
            // 
            if (this.width) {
                _style.width = typeof this.width === 'number' ? `${this.width}px` : this.width;
            }
            // 
            if (this.height) {
                _style.height = typeof this.height === 'number' ? `${this.height}px` : this.height;
            }
            // z-index
            if (this.zIndex) {
                _style.zIndex = this.zIndex;
            }
            return _style;
        }
    },
    methods: {
    // 
        destory() {
            // 
            document.body.removeChild(this.$parent.$el);
        },
        // 
        clickCancel: function () {
            // 
            Object.prototype.toString.call(this.cancel.callback) === '[object Function]' && this.cancel.callback();
            // 
            this.destory();
        },
        // 
        clickConfirm() {
            // 
            Object.prototype.toString.call(this.confirm.callback) === '[object Function]' && this.confirm.callback(this.type === 'prompt' ? this.currentValue : '');
            // 
            this.destory();
        }
    }
};
export default module;



