export default {
    data() {
        return {
            // 
            sCurrentScrollTop:0,
            // 
            sScrollTopReduce:0
        };
    },
    // computed: {
    //     // 
    //     style() {
    //         return {
    //             // top: `${this.sCurrentScrollTop - this.sScrollTopReduce}px`,
    //             transform: `${this.sCurrentScrollTop - this.sScrollTopReduce}px`,
    //             msTransform: `${this.sCurrentScrollTop - this.sScrollTopReduce}px`
    //         };
    //     }
    // },
    methods:{
        // 
        sSetCurrentScrollTop(top) {
            // 
            this.sCurrentScrollTop = typeof top === 'number' ? top : this.sCurrentScrollTop;
        },
        // 
        sSetScrollTopReduce(scrollTopReduce) {
            // 
            this.sScrollTopReduce = scrollTopReduce;
        },
        // 
        sSetNoDataLineStatus(noDataLine) {
            if (this.hasLine) {
                this.noLevel1 = false;
                this.noLevel2 = false;
                this.noLevel3 = false;
                this.noLevel4 = false;
                this.noLevel5 = false;
                // 
                noDataLine.value.every((item, index) => {
                    // 
                    this[`noLevel${index}`] = item;
                    return item;
                });
                let _levelArray = [];
                // 
                for (const name in noDataLine.level) {
                    _levelArray.push(parseInt(name, 10));
                }
                // 
                _levelArray = _levelArray.sort((a, b) => a - b > 0 ? 1 : -1);
                // 
                let _minValue = _levelArray[0];
                // 
                let _fisrtItem = null;
                // 
                let _fisrtItemP = null;
                this.currentDataList.every((item) => {
                    // 
                    if (item.level === _minValue) {
                        _fisrtItem = item;
                        return false;
                    }
                    return true;
                });
                // 
                if (_fisrtItem) {
                    // 
                    do {
                        _minValue--;
                        _fisrtItemP = this.cGetCache(_fisrtItem[this.dpKey]);
                        // 
                        if (_fisrtItemP && _fisrtItemP.last) {
                            // 
                            this[`noLevel${_minValue}`] = false;
                        }
                        _fisrtItem = _fisrtItemP;
                    } while (_minValue > 0);
                }
            }
        }
    }
};



