export default {
    data() {
        return {};
    },
    watch: {
        //  to form
        $route(to) {
            // debugger
            const code = to.params.id;
            // id
            this.setCurrentRouterId(code);
        }
    }
};

/**
 
  beforeRouteLeave 
  beforeEach 
  beforeRouteUpdate  (2.2+)
  beforeEnter
 
  beforeRouteEnter
  beforeResolve  (2.5+)
 
  afterEach 
  DOM 
  beforeRouteEnter  next 
 */



