/**
 *@version 1.0.1
 *@author wuwg
 *@createTime 2019/8/28 - 10:57
 *@updateTime 2019/8/28 - 10:57
 *@see [jsDoc]{@link  http://www.dba.cn/book/jsdoc/JSDOCKuaiBiaoQianBLOCKTAGS/CONSTRUCTS.html}
 @description template 
 */
import allScan from './all-scan/index.vue';
import singleTable from './table/single-table/index.vue';
import formTable from './table/form-table/index.vue';
import fromTableGroup from './table/form-table-group/index.vue';
import moreTable from './table/more-table/index.vue';
// form table  
// import fromTableMixture from '../../unit-test/from-table-mixture/index.vue';
import categoryFormTable from './table/category-form-table/index.vue';
import dynamicComponent from './dynamic-component/index.js';
// 
// 
import globalEvent from './extend/globalEvent.js';
// 
import globalMethods from './extend/globalMethods.js';
// 
import routerConfig from './extend/router-config.js';
// 
import breadcrumbConfig from './extend/breadcrumb-config.js';
// 
import request from './request/request.js';
// 
const emptyDataComponent = {props: {pageInitFlag: {type: Boolean, default: false}}, template: '<ac-empty :show="pageInitFlag"></ac-empty>'};

// 
export default {
    // components
    components: {
        singleTable,
        formTable,
        fromTableGroup,
        categoryFormTable,
        // fromTableMixture,
        dynamicComponent,
        moreTable
    },
    mixins: [globalEvent, globalMethods, routerConfig, breadcrumbConfig, request],
    // 
    props: ['id', 'params', 'query'],
    data() {
        return {
            // 
            pageInitFlag: true,
            // 
            // currentComponent: emptyDataComponent,
            currentComponent: null,
            // 
            pageData: null,
            // 
            isNavigation: false,
            // ajax 
            ajaxOptions: {
                url: window.fdConfig.url.getData,
                data: {},
                method: window.fdConfig.methodPost
            },
            // 
            pageType: ''
        };
    },
    created() {
        // 1.limit
        this.initLimit();
        // 2.
        this.initOperate();
    },
    // watch
    // 
    methods: {
        // 
        scrollbarReady(scrollbar) {
            this.scrollbar = scrollbar;
        },
        // 
        scrollUpdate() {
            if (this.scrollbar) {
                this.scrollbar.update(0, 0);
            }
        },
        // limitlimit
        initLimit() {
            const clientHeight = document.body.clientHeight;
            //  -  -  - padding-top - padding-bottom -  -  - 
            // eslint-disable-next-line
            // const mainContentHeight = clientHeight - 71 - 75 - 16 - 30 - 40 - 43 - 52;
            const mainContentHeight = clientHeight - 300;
            // 
            // eslint-disable-next-line
            window.fdConfig.limit = Math.floor(mainContentHeight / 63);
        },
        // ;
        initOperate() {
            // id ,query +  num + Name
            if (this.id) {
                //  asideglobalBus  
                // all
                if (this.id === 'all') {
                    // 
                    this.setCommonActiveNodeInfo(true, {
                        treeItem: {
                            name: '',
                            code: 'all'
                        }
                    });
                } else if (this.query.nav) {
                    // 1. 
                    // 
                    this.initBreadList();
                    // 2.
                    this.setCurrentComponent(this.id);
                }
            }
        },
        // ajax data 
        getTreeAjaxOptions(id) {
            const _ajaxOptions = {
                // ajaxurl
                url: this.ajaxOptions.url,
                // ajax
                method: this.ajaxOptions.method,
                // ajax
                data: {
                    // idStringtype===tree
                    sjyId: id,
                    // treetableform-table
                    // pId: query.pId,
                    type: 'tree',
                    // 
                    limit: window.fdConfig.limit,
                    // 0
                    offset: 0
                }
            };
            return _ajaxOptions;
        },

        // table 
        getTableAjaxOptions(dataItem) {
            const _ajaxOptions = {
                // ajaxurl
                url: this.ajaxOptions.url,
                // ajax
                method: this.ajaxOptions.method,
                // ajax
                data: {
                    // idStringtype===tree
                    sjyId: this.id,
                    // treetableform-table
                    type: dataItem.type,
                    // 
                    limit: window.fdConfig.limit,
                    // 0
                    offset: 0,
                    // id Stringtype===table
                    id: dataItem.trId,
                    // layoutIdStringtype===table
                    layoutId: dataItem.layoutId,
                    // indexStringtype===form-table
                    // @todo  index esIndex yapi
                    // esIndex: query.esIndex,
                    //  Stringtype===form-table
                    cxdh: dataItem.cxdh,
                    // linkType=view||download||'subtable', 
                    linkType: 'view'
                }
            };
            return _ajaxOptions;
        },

        // form-table 
        getFormTableAjaxOptions(dataItem) {
            const _ajaxOptions = {
                // ajaxurl
                url: this.ajaxOptions.url,
                // ajax
                method: this.ajaxOptions.method,
                // ajax
                data: {
                    // idStringtype===tree
                    sjyId: this.id,
                    // treetableform-table
                    type: dataItem.type,
                    // 
                    limit: window.fdConfig.limit,
                    // 0
                    offset: 0,
                    // id Stringtype===table
                    id: dataItem.id,
                    // layoutIdStringtype===table
                    layoutId: dataItem.layoutId,
                    // esIndexStringtype===form-table
                    // @todo  index esIndex yapi
                    index: dataItem.index,
                    //  Stringtype===form-table
                    cxdh: dataItem.trId,
                    // trIdStringtype===form-table
                    // @todo trId cxdh 
                    trId: dataItem.trId
                }
            };
            return _ajaxOptions;
        },
        // 
        setCurrentComponent(id) {
            // 1. 
            const _targetNavItem = this.breadList.slice(this.breadList.length - 1)[0];
            // 2. 
            const _navExtData = _targetNavItem.ext;
            // 3. 
            const _type = _navExtData.type;
            // id === all 
            if (id === 'all') {
                this.currentComponent = allScan;
            } else {
                let _ajaxOptions = {};
                // 
                switch (_type) {
                    // _type ===  tree 
                    case 'tree':
                        // ajax data
                        _ajaxOptions = this.getTreeAjaxOptions(id);
                        break;
                    case 'table':
                        // table 
                        _ajaxOptions = this.getTableAjaxOptions(_navExtData);
                        break;
                    case 'form-table':
                        // form-table 
                        _ajaxOptions = this.getFormTableAjaxOptions(_navExtData);
                        break;
                    default:
                        break;
                }
                // 
                window.fdConfig.queryCriteria = _ajaxOptions.data;
                // 
                this.handleRequestGetData(_ajaxOptions);
            }
        },
        // 
        handleRequestGetData(ajaxOptions) {
            const _this = this;
            // 
            this.requestGetData(ajaxOptions)
                .then(data => {
                    // 
                    _this.processData(data);
                });
        },
        // 
        processData(resultData) {
            const _this = this;
            // const _query = this.$route.query;
            // const query = JSON.parse(decodeURIComponent(_query.nav)).slice(-1)[0].ext;
            const breadQuery = this.breadList[this.breadList.length - 1];
            const query = breadQuery.ext;
            //  
            if (window.fdConfig.isDebug) {
                // true
                let requireUrl = '';
                switch (query.type) {
                    case 'tree':
                        requireUrl = this.id;
                        break;
                    case 'table':
                        //  
                        requireUrl = `${this.id}layoutId${query.layoutId}trId${query.trId}`;
                        break;
                    case 'form-table':
                        //
                        requireUrl = `${this.id}index${query.index}cxdh${query.trId}id${query.id}`;
                        break;
                    default:
                        break;
                }
                // console.log(requireUrl);
                const requireData = {
                    url: resultData[requireUrl],
                    data: {},
                    method: window.fdConfig.methodGet
                };
                // id mock
                _this.requestGetDataForUrl(requireData)
                    .then(childData => {
                        // 
                        _this.initPage(childData);
                    });
            } else {
                // 
                _this.initPage(resultData);
            }
        },
        // 
        initPage(pageData) {
            const _this = this;
            if (!pageData.pageType) {
                window.console.error('');
            }
            // 
            _this.pageData = pageData.data;
            // 
            _this.isNavigation = pageData.navigation !== null ? pageData.navigation : false;
            switch (pageData.pageType) {
                case 'table':
                    this.currentComponent = singleTable;
                    break;
                case 'form-table':
                    this.currentComponent = formTable;
                    break;
                case 'form-table-group':
                    this.currentComponent = fromTableGroup;
                    break;
                case 'category-form-table':
                    this.currentComponent = categoryFormTable;
                    break;
                case 'more-table':
                    this.currentComponent = moreTable;
                    break;
                default:
                    break;
            }
            // 
            if (this.$refs.jsDynamicComponent && typeof this.$refs.jsDynamicComponent.update === 'function') {
                this.$refs.jsDynamicComponent.update(this.pageData, pageData.pageType);
            }
            //  
            // this.emitGlobalEvent(() => {
            //     // 
            //     this.globalBus.$emit('global-common-update-data', this.treeData);
            // });
        }
    }
};



