/**
 * @param{function} fn  
 * @param {object}_this 
 * @param {number} delay 
 * @description  
 * @returns {Function} 
 */
export default (fn, _this, delay) => {
    let waiting;
    const _delay = 50;
    const later = (_arguments) => {
        waiting = false;
        fn.apply(_this, _arguments);
    };
    return () => {
        const _arguments = arguments;
        if (waiting) {
            clearTimeout(waiting);
        }
        // 
        waiting = setTimeout(() => {
            later(_arguments);
        }, typeof delay === 'number' ? delay : _delay);
    };
};



