export default {
    data() {
        return {
            // 
            edListDragPreview: null,
            // 
            serverPort: ''
        };
    },
    methods: {
        // 
        setTreeDragContent() {
            let _treeDragContent = [];
            let _class = this.dragNode.treeNodeData.type;
            switch (this.dragNode.treeNodeData.type) {
                //       //    // 
                case 'volume':
                case 'booklet':
                case 'directory':
                    _class = `${_class} icon text`;
                    _treeDragContent.push(`<div class="${_class}">${this.dragNode.treeNodeData.name}</div>`);
                    break;
                    //  // 
                case 'material':
                case 'catalogmaterial':
                case 'transitmaterial': {
                    _class = `${_class} preview`;
                    _treeDragContent.push(`<div class="${_class}">`);
                    let imgSrc;
                    if (this.dragNode.treeNodeData.details.thumbnail) {
                        imgSrc = this.serverPort + this.dragNode.treeNodeData.details.thumbnail;
                    } else {
                        imgSrc = '/static/img/lost-wj.png';
                    }
                    _treeDragContent.push(`<img  src="${imgSrc}"  alt="${this.tree.dragNode.treeNodeData.name}"/>`);
                    _treeDragContent.push('</div>');
                    break;
                }
                // 
                case 'attachment': {
                    _class = `${_class} preview`;
                    _treeDragContent.push(`<div class="${_class}">`);
                    let imgSrc;
                    switch (this.dragNode.treeNodeData.details.type) {
                        case 'mp3':
                            imgSrc = '/static/img/thumb-mp3.png';
                            break;
                        default:
                            imgSrc = '/static/img/thumb-vedio.png';
                            break;
                    }
                    _treeDragContent.push(`<img  src="${imgSrc}"  alt="${this.tree.dragNode.treeNodeData.name}"/>`);
                    _treeDragContent.push('</div>');
                    break;
                }
                default :
                    break;
            }
            // 
            if (this.edMultiple) {
                const _len = this.edMultipleDragData.length;
                _class = 'fd-multiple-preview';
                _treeDragContent = [];
                _treeDragContent.push(`<div class="${_class}"><span class="fd-count">${_len}</span></div>`);
            }
            // 
            this.treeDragContent.innerHTML = _treeDragContent.join('');
        },
        // 
        // 
        /**
         * @param {boolean} flag 
         * @returns {*} 
         * 
         */
        edSetDragPreviewStatus(flag) {
            this.edListDragPreview.style.display = flag ? 'block' : 'none';
            // 
            // this.edListDragPreview.setAttribute('show', flag);
            //   originTree 2019-05-21
            /* if (flag) {
             // 
             this.edListDragPreview.innerHTML = this.edSetDragPreviewHtmlCallback(this.edCurrentDragDataItem, this);
             }*/
        },

        // 
        edSetDragPreviewPosition(event) {
            // 
            const _distanceX = 5;
            const _distanceY = 10.5;
            const style = {
                top: `${event.pageY + _distanceX}px`,
                left: `${event.pageX + _distanceY}px`
            };
            // 
            for (const _name in style) {
                // 
                this.edListDragPreview.style[_name] = style[_name];
            }
        },
        // 
        createTreeDragContent: function () {
            const _dragPreview = document.createElement('div');
            // class
            _dragPreview.setAttribute('class', 'fd-tree-node-preview');
            // id
            _dragPreview.setAttribute('id', this.edPreviewId.replace('#', ''));
            // body
            document.body.appendChild(_dragPreview);
            return _dragPreview;
        },
        // 
        edMakeDragPreview() {
            if (document.querySelector(this.edPreviewId)) {
                // 
                this.edListDragPreview = document.querySelector(this.edPreviewId);
                this.treeDragContent = this.edListDragPreview;
            } else {
                // 
                this.edListDragPreview = this.createTreeDragContent();
                // 
                this.treeDragContent = this.edListDragPreview;
            }
            // 
            this.edSetDragPreviewStatus(false);
        },
        // 
        edDestroyDragPreview() {
            // body
            // document.body.removeChild(this.edListDragPreview);
        }
    }
};



