export default {
    data() {
        return {
            //  
            breadList: []
        };
    },
    // 
    methods: {
        // 
        breadClickItem(item, index) {
            // 
            // 1. 
            this.breadList.splice(index + 1);
            // newQuery.index = 2;
            // 2.
            this.$router.replace({query: {nav: encodeURIComponent(JSON.stringify(this.breadList))}});
            // 3. 
            this.setCurrentComponent(this.id);
        },
        // 
        breadSetList(dataList) {
            // 
            /*   {
             name:'home',
             disabled: false,
             className:'test1'
            }*/
            // 
            this.breadList = dataList;
        },

        // 
        initBreadList() {
            const _query = this.$route.query;
            let _dataList = [];
            try {
                _dataList = JSON.parse(decodeURIComponent(_query.nav));
            } catch (e) {
                window.console.error('url');
            }
            // 
            this.breadSetList(_dataList);
        }
    }

};



