/**
 * @description 
 */
// vue
import Vue from 'vue';
// 
import config from '@/components/config/component-config.js';
import globalBus from '@/components/globalBus/index.js';
// 

// basic start
import button from './basic/button/index.vue';
// basic end


// navigation start
import tree from './navigation/tree/index.vue';
import tab from './navigation/tab/index.vue';
import fixNav from './navigation/fix-nav/index.vue';
import breadcrumb from './navigation/breadcrumb/index.vue';
// navigation end

// data start
import table from './data/table/index.vue';
import formTable from './data/form-table/index.vue';
// data end

// form start
import autoComplete from './form/auto-complete/index.vue';
// form end

// notice start
import message from '@/components/common/notice/message/message.js';
import ajaxLoading from '@/components/common/notice/ajax-loading/ajax-loading.js';
import $alert from '@/components/common/notice/alert/alert.js';
// notice end

// other start
import scrollbar from './other/scrollbar/index.vue';
// other end
const components = {button, scrollbar, tree, tab, fixNav, breadcrumb, table, formTable, autoComplete};

// 
const install = function () {
    if (install.installed) {
        return false;
    }
    let i = 0;
    // 
    Object.keys(components)
        .forEach(key => {
            i++;
            const pattern = /([A-Z])|^(\$)/gm;
            let _key = key;
            // -
            _key = _key.replace(pattern, function (a) {
                // ==$ $
                return a === '$' ? '' : `-${a.toLowerCase()}`;
            });
            // 
            Vue.component(`${config.componentCcPrefix}-${_key}`, components[key]);
            window.console.log(`${config.componentCcPrefix}-${_key}`);
        });
    // 
    Vue.prototype.$Message = message;
    Vue.prototype.$AjaxLoading = ajaxLoading;
    Vue.prototype.$Alert = $alert;
    // eventBus
    Vue.prototype.globalBus = globalBus;
    window.console.log(`${i}`);
};
const API = {install};
Vue.use(API);



