/**
 * @version 20190401
 * @author wuwg
 * @createTime  20201127
 * @updateTime  20201127
 * @decription  
 */
export default {
    methods: {
        // 
        emitEvent(eventName, event, targetNode, targetPNode, targetIndex) {
            switch (eventName) {
                //   ():
                case 'dragstart':

                    /**
                     * #event
                     *
                     * @name    dragstart
                     * @param   targetNode   
                     * @param   event       event
                     * @body    console.log("");
                     * @description    
                     */
                    this.$emit('dragstart', event, targetNode, this);
                    break;
                //   ():
                case 'drag':

                    /**
                     * #event
                     *
                     * @name    drag
                     * @param   targetNode   
                     * @param   event       event
                     * @body    console.log("");
                     * @description    
                     */
                    this.$emit('drag', event, targetNode, this);
                    break;
                //   ():
                case 'dragend':

                    /**
                     * #event
                     *
                     * @name    dragend
                     * @param   targetNode   
                     * @param   hoverNode   
                     * @param   dragNode    
                     * @param   targetPostion    
                     * @param   event       event
                     * @body    console.log("");
                     * @description    
                     */
                    this.$emit('dragend', event, targetNode, this.hoverNode, this.dragNode, this.targetPostion, this);
                    break;
                //   (:)
                case 'dragenter':

                    /**
                     * #event
                     *
                     * @name    dragenter
                     * @param   targetNode   
                     * @param   dragNode    
                     * @param   event       event
                     * @body    console.log("dragenter!");
                     * @description    
                     */
                    this.$emit('dragenter', event, targetNode, this.dragNode, this);
                    break;
                //   (:)
                case 'dragover':

                    /**
                     * #event
                     *
                     * @name    dragover
                     * @param   targetNode   ()
                     * @param   dragNode    
                     * @param   event       event
                     * @body    console.log("dragover")!
                     * @description    
                     */
                    this.$emit('dragover', event, targetNode, this.dragNode, this);
                    break;
                //   (:)
                case 'dragleave':

                    /**
                     * #event
                     *
                     * @name    dragleave
                     * @param   targetNode    (  )
                     * @param   dragNode    
                     * @param   event       event
                     * @body    console.log("dragleave")!
                     * @description    
                     */
                    this.$emit('dragleave', event, targetNode, this.dragNode, this);
                    break;

                //   (:)
                case 'drop':

                    /**
                     * #event
                     *
                     * @name    drop
                     * @param   targetNode    (   )
                     * @param   hoverNode    
                     * @param   dragNode    
                     * @param   targetPostion     middle ,top bottom
                     * @param   event       event
                     * @body     console.log("drop")!
                     * @description    
                     */
                    this.$emit('drop', event, targetNode, this.hoverNode, this.dragNode, this.targetPostion, this);
                    break;
                // 
                case 'contextmenu':

                    /**
                     * #event
                     *
                     * @name    contextmenu
                     * @param   targetNode   
                     * @param    event       event
                     * @body     console.log("contextmenu")!
                     * @description    
                     */
                    this.$emit('contextmenu', event, targetNode, this);
                    break;
                // 
                case 'mend-name':

                    /**
                     * #event
                     *
                     * @name    mend-name
                     * @param   targetNode    
                     * @param   event    event
                     * @body    console.log("mend-name")!
                     * @description    
                     */
                    this.$emit('mend-name', event, targetNode, this);
                    break;
                // 
                case 'mend-name-fail':

                    /**
                     * #event
                     *
                     * @name    mend-name-fail
                     * @param   targetNode    
                     * @param   event    event
                     * @body    console.log("mend-name-fail")!
                     * @description    
                     */
                    this.$emit('mend-name-fail', event, targetNode, this);
                    break;
                // select  click
                case 'click':
                    // 
                    this.$emit('click', event, targetNode, this);
                    break;
                // 
                case 'select':
                    // 
                    this.$emit('select', event, targetNode, this);
                    break;
                // 
                case 'clickExtendIcon':
                    // 
                    this.$emit('clickExtendIcon', event, targetNode, targetPNode, targetIndex, this);
                    break;
                //  
                case 'dblclick':
                    // 
                    this.$emit('dblclick', event, targetNode, this);
                    break;
                // 
                case 'drop-fail':
                    // 
                    this.$emit('drop-fail', event, targetNode, this);
                    break;
                //  
                case 'blur':
                    // 
                    this.$emit('blur', event, targetNode, this);
                    break;
                //  
                case 'focus':
                    // 
                    this.$emit('focus', event, targetNode, this);
                    break;
                //  
                case 'enter':
                    // 
                    this.$emit('enter', event, targetNode, this);
                    break;
                // dom
                case 'ready':
                    // 
                    this.$emit('ready', this);
                    break;
                //  
                case 'update':
                    // 
                    this.$emit('update', this);
                    break;
                //  
                case 'refresh':
                    // 
                    this.$emit('refresh', this);
                    break;
                //  
                case 'checked':
                    // 
                    this.$emit('checked', targetNode, this);
                    break;
                //    extend
                case 'extend':
                    // 
                    this.$emit('extend', targetNode, this);
                    break;
                //   collapse
                case 'collapse':
                    // 
                    this.$emit('collapse', targetNode, this);
                    break;
                //   collapse
                case 'scroll':
                    // 
                    this.$emit('scroll', this.sCurrentScrollTop, 0, this);
                    break;
                default :
                    break;
            }
        }
    }
};



