/**
 * 
 * @param {String} str 
 * @returns {*}  Number
 */
function getStrLength(str) {
    // "g" 
    return str.replace(/[\u0391-\uFFE5]/g, 'aa').length;
}

export default {
    data() {
        return {
            tName: '',
            tDpKey: 'pcode',
            tDKey: 'code',
            tDName: 'name',
            tDChildren: 'children',
            tDData: [],
            tHasLine: false,
            tHasIcon: true,
            tTree: null,
            tActiveCode: '',
            tCheckedCode: [],
            tTargetCheckedNode: null,
            // 
            treeData: []
        };
    },
    mounted() {
        // // 
        // this.globalBus.$on('global-change-tree-active', function (code) {
        //     _this.tActiveCode = code;
        //     _this.tTree.gSetActiveId(code);
        // });
    },
    methods: {
        tReady(treeObject) {
            // 
            this.tTree = treeObject;
            // 
            this.tLoadTreeData();
        },
        // 
        tSelect(event, treeItem) {
            // 
            // materialdirectory
            if (treeItem && treeItem.type === 'material') {
                this.tActiveCode = treeItem[this.tDKey];
                // 
                const activeInfoObj = {
                    // pId: pId,
                    treeItem: treeItem
                };
                // 
                this.emitGlobalEventSelectNode(event, activeInfoObj);
            } else {
                // this.tActiveCode = '';
            }
        },
        // ajax
        tLoadTreeData() {
            // 
            this.requestDataList(this.ajaxOptions, this.tName)
                .then((serverData) => {
                    // 
                    this.tUpdateTreeData(serverData);
                });
        },
        // 
        tUpdateTreeData(treeData) {
            this.treeData = treeData;
            // 
            this.tTree.update(treeData, () => {
                // 
                const _activeCode = this.tActiveCode;
                // 
                if (_activeCode && _activeCode !== 'all') {
                    // 
                    this.tTree.gSetActiveId(_activeCode);
                }
                // else if (_activeCode === 'all') {
                //     // 
                //     this.globalBus.$emit('global-common-activeNodeInfo', {
                //         [this.tDKey]: 'all',
                //         [this.tDName]: ''
                //     });
                // }
            });
        },
        // class
        tAddClassCallback(item) {
            return [item.type, item.children.length === 0 ? 'directory-empty' : ''];
        },
        tGAdjustEllipsisHandler(dataItem) {
            let _flag = true;
            switch (dataItem.level) {
                // 
                case 0:
                    _flag = getStrLength(dataItem.name) > 18; // eslint-disable-line
                    break;
                // 
                case 1:
                    _flag = getStrLength(dataItem.name) > 18; // eslint-disable-line
                    break;
                // 
                // eslint-disable-next-line no-magic-numbers
                case 2:
                    _flag = getStrLength(dataItem.name) > 18; // eslint-disable-line
                    break;
                default:
                    _flag = false;
                    break;
            }

            return _flag;
        }
    }

};



