/**
 * #info
 *
 * @author wuwg
 * @createTime 2018/03/20
 * @updateTime   2018/05/08
 * @description module component
 */

// prefix
const prefixCls = 'fd-btn';
const config = {prefix: 'fd-'};

/**
 * #item
 *
 * @type aty-button
 * @cn 
 * @order 100
 * @stPlugin true
 * @nameprop text
 * @children [aty-icon,aty-text]
 */
const module = {
    props: {

        /**
         * #property
         *
         * @name text
         * @cn 
         * @type  string
         * @group 
         * @value button
         * @description 
         */
        text: {
            type: String,
            default: 'button'
        },

        /**
         * #property
         *
         * @name  isHtml
         * @cn    html
         * @type  boolean
         * @group 
         * @value false
         * @description   isHtml=true, html,
         */
        isHtml: {
            type: Boolean,
            default: false
        },

        /**
         * #property
         *
         * @name type
         * @cn 
         * @type list
         * @group 
         * @options [primary, ghost, dashed, text, info, success, warning, error, default, cancel]
         * @value default
         * @description 
         */
        type: {
            validator(value) {
                return ['primary', 'ghost', 'dashed', 'text', 'info', 'success', 'warning', 'error', 'default', 'cancel'].indexOf(value) !== -1;
            }
        },

        /**
         * #property
         *
         * @name shape
         * @cn 
         * @type list
         * @group 
         * @options [circle]
         * @description circle
         */
        shape: {
            validator(value) {
                return ['circle'].indexOf(value) !== -1;
            }
        },

        /**
         * #property
         *
         * @name long
         * @cn 
         * @type boolean
         * @group 
         * @value false
         * @description  100%
         */
        long: {
            type: Boolean,
            default: false
        },

        /**
         * #property
         *
         * @name loading
         * @cn  
         * @type boolean
         * @group 
         * @value false
         * @description 
         */
        loading: Boolean,

        /**
         * #property
         *
         * @name disabled
         * @cn 
         * @type boolean
         * @group 
         * @value false
         * @description 
         */
        disabled: Boolean,

        /**
         * #property
         *
         * @name htmlType
         * @cn html
         * @type list
         * @group 
         * @value button
         * @options [button, submit, reset]
         * @description typebuttonsubmitreset
         */
        htmlType: {
            default: 'button',
            validator(value) {
                return ['button', 'submit', 'reset'].indexOf(value) !== -1;
            }
        },

        /**
         * #property
         *
         * @name icon
         * @cn 
         * @type icon
         * @group 
         * @description  
         */
        icon: String
    },
    data() {
        return {
            className: `${config.prefix}-button`,
            showSlot: true
        };
    },
    computed: {
        // class
        iconClasses() {
            return `${`${config.prefix}-icon`} ${`${config.prefix}-icon`}-${this.icon}`;
        },
        //  class
        iconLoadingClasses() {
            return `${`${config.prefix}-icon`} ${`${config.prefix}-icon-load-c`} ${`${config.prefix}-load-loop`}`;
        },
        // class
        classes() {
            return [
                `${prefixCls}`,
                {
                    // 
                    [`${prefixCls}-${this.type}`]: Boolean(this.type),
                    // 
                    [`${prefixCls}-long`]: this.long,
                    // 
                    [`${prefixCls}-${this.shape}`]: Boolean(this.shape),
                    //  ...
                    [`${prefixCls}-loading`]: this.loading !== null && this.loading,
                    // 
                    [`${prefixCls}-icon-only`]: !this.showSlot && (Boolean(this.icon) || this.loading)
                }
            ];
        }
    },
    methods: {

        /**
         * @cn 
         * @param {object} event click
         * @description 
         * @returns {void} 
         */
        handleClick(event) {
            this.$el.blur();
            this.$emit('click', event);
        }
    },
    mounted() {
        // 
        this.showSlot = Boolean(this.$slots.default);
    }
};
export default module;



