/**
 *@file index
 *@version 1.0.0
 *@author wuwg
 *@createTime 2020/12/08 - 11:33
 *@updateTime 2020/12/08 - 11:33
 *@see [jsDoc]{@link  http://www.dba.cn/book/jsdoc/JSDOCKuaiBiaoQianBLOCKTAGS/CONSTRUCTS.html}
 @description  v-line-clamp  
 */
// css
import './line-clamp.less';
// 
import browser from '../../../../components/tools/extend/browser.js';
// chrome
const isChrome = browser.isChrome();

/**
 * domcss
 * @param {object} element  dom
 * @param {String} attr  
 * @returns {*} 
 */
function getStyle(element, attr) {
    if (element.currentStyle) {
        return element.currentStyle[attr];
    }
    return getComputedStyle(element, false)[attr];
}

/**
 * 
 * @param {object}  el dom
 * @param {object} binding  binding 
 * @returns {void} 
 */
function setElStyle(el, binding) {
    // 
    const _lineClamp = binding.value;
    // 
    const _lineHeight = parseInt(getStyle(el, 'line-height'), 10);
    // 
    const _totalHeight = _lineClamp * _lineHeight;
    // 
    const _firstChild = el.firstChild;
    // , chrome  offsetHeightchrome scrollHeight = 0 scrollHeight
    // const _clientHeight = _firstChild.scrollHeight || _firstChild.offsetHeight;
    const _clientHeight = Math.max(_firstChild.offsetHeight, _firstChild.scrollHeight);
    // name ...
    if (_clientHeight > _totalHeight) {
        // window.console.log('%c', 'color:red;font-size:20px;');
        // class
        el.classList.add('fd-line-clamp');
        // chrome  
        // @todo   2020-12-09
        if (!isChrome) {
            // 
            el.style.height = `${_totalHeight}px`;
            // 
            el.classList.add('other-line-clamp');
            // 
            _firstChild.classList.add('fd-line-clamp-text');
        } else {
            el.style['-webkit-line-clamp'] = _lineClamp;
        }
        // ...
        el.dataset.ellipsis = true;
    }
}

// 
export default {
    inserted: function (el, binding) {
        // 
        setElStyle(el, binding);
    },
    //  VNode  VNode 
    //  ()
    update: function (el, binding) {
        // 
        setElStyle(el, binding);
    },
    componentUpdated: function (el, binding) {
        // 
        setElStyle(el, binding);
    }
};



