// 
import treeConfig from './extend/treeConfig.js';
import globalEvent from './extend/globalEvent.js';
// 
import routerConfig from './extend/router-config.js';
// 
import request from './request/request.js';
// import {Dictionary} from 'vue-router/types/router';

export default {
    mixins: [treeConfig, request, globalEvent, routerConfig],
    data() {
        return {
            // 
            showLog: window.fdConfig.showLog,
            // ajax 
            ajaxOptions: {
                // 
                url: window.fdConfig.url.tree,
                data: {id: window.fdConfig.jbxxbhId},
                method: window.fdConfig.methodGet
            },
            // 
            placeholder: '',
            // Id
            currentRouterId: '',
            // 
            exportTipTitle: '',
            // 
            exportExpireTime: '',
            // RegExp
            pattern: '^(?=.*\\d)(?=.*[a-zA-Z]).{10,100}$',
            // 
            patternMessage: '1010',
            // 
            rules: {
                exportPassword: [{
                    required: true,
                    validator: function (rule, value, callback) {
                        if (value === '') {
                            callback(new Error(''));
                        } else if (!(/^(?=.*\d)(?=.*[a-zA-Z]).{10,100}$/).test(value)) {
                            callback(new Error('1010'));
                        }
                        callback();
                    },
                    message: '1010',
                    trigger: 'blur'
                }]
            },
            exportPassword: '',
            //  allnew
            exportType: '',
            // 
            isAllScan: true,
            // 
            exportTip: ''
        };
    },
    created() {
        // 
        this.initOperateRouter();
    },
    methods: {
        // 
        initOperateRouter() {
            const _route = this.$route;
            //  author lfan 2020-10-19
            // let newQuery = JSON.parse(JSON.stringify(_route.query));
            // this.$router.replace({
            //     name?: this.$router.currentRoute
            //     path?: string
            //     hash?: string
            //     query?: Dictionary<string | (string | null)[] | null | undefined>
            //     params?: Dictionary<string>
            //     append?: boolean
            //     replace?: boolean
            //     query: this.$router.currentRoute.query
            // });
            // alert(window.location.href)
            // this.$router.replace(window.location.href);
            // Id
            this.setCurrentRouterId(_route.params.id);
        },
        // Id
        setCurrentRouterId(id) {
            // debugger;
            this.currentRouterId = id;
            // 
            if (this.currentRouterId === 'all') {
                this.clickAllScan(false);
            } else {
                this.isAllScan = false;
                // 
                // eslint-disable-next-line no-lonely-if
                if (this.currentRouterId !== this.tActiveCode && this.tTree) {
                    // 
                    this.tTree.gSetActiveId(this.currentRouterId);
                } else {
                    this.tActiveCode = this.currentRouterId;
                    // if (this.commonActiveNodeInfo) {
                    //     // 
                    //     const activeInfoObj = {
                    //         // pId: pId,
                    //         treeItem: this.commonActiveNodeInfo
                    //     };
                    //     // 
                    //     this.emitGlobalEventSelectNode(activeInfoObj);
                    // }
                }
            }
        },
        // 
        remoteRequestMethod() {
            // 
            // if (!Array.isArray(this.dOneDimensionalListMaterial)) {
            //     this.dOneDimensionalListMaterial = [];
            // }
            // if (!this.dOneDimensionalListMaterial.length && this.tTree) {
            //     // 
            //     this.dOneDimensionalListMaterial = this.tTree.dOneDimensionalList.filter((item) => item.type === 'material');
            // }
            return new Promise((resolve) => {
                // console.log(this.tTree.dOneDimensionalList)
                resolve(this.tTree.dOneDimensionalList);
                // resolve(this.dOneDimensionalListMaterial);
            });
        },
        // 
        selectAutoComplete(item) {
            const _id = item.item[this.tDKey];
            // 
            // this.tTree.gSetActiveId(_id);
            const _dataItem = this.tTree.cGetCache(_id);
            // 
            this.tTree.gSetFocusItem(_dataItem);
        },
        // 
        clickAllScan(event) {
            // debugger
            // 
            if (this.tTree) {
                this.tTree.gSetActiveId('');
            }
            // 
            const activeInfoObj = {
                // pId: '',
                treeItem: {
                    [this.tDKey]: 'all',
                    [this.tDName]: ''
                }
            };
            // 
            this.emitGlobalEventSelectNode(event, activeInfoObj);
            this.isAllScan = true;
        },
        // 
        handleExport(type) {
            switch (type) {
                case 'all':
                    this.exportTipTitle = '';
                    this.exportType = 'all';
                    break;
                case 'new':
                    this.exportTipTitle = '';
                    this.exportType = 'new';
                    break;
                default:
                    break;
            }
            // 
            const ajaxExportOptions = {
                url: window.fdConfig.url.isExport,
                data: {
                    id: window.fdConfig.jbxxbhId,
                    type: this.exportType
                },
                method: window.fdConfig.methodGet
            };
            this.requestIsExportList(ajaxExportOptions)
                .then(data => {
                    // 
                    if (data.isExport === true) {
                        this.$refs.modalExport.open();
                        this.$refs.exportForm.clearValidateInfo();
                    } else {
                        this.$refs.tipModal.open();
                        this.exportTip = data.message;
                        // setTimeout(() => {
                        //     this.$refs.tipModal.close();
                        // }, 3000);
                    }
                });
            // const url = window.fdConfig.url.isExport;
            // axios.get(url, {
            //     params:{
            //         id: this.ajaxOptions.data.id,
            //         type: this.exportType
            //     }
            // })
            //     .then(resp => {
            //         if (resp.data.isExport === true) {
            //             this.$refs.modalExport.open();
            //         } else {
            //             this.$refs.tipModal.open();
            //             this.exportTip = resp.data.message;
            //             setTimeout(() => {
            //                 this.$refs.tipModal.close();
            //             }, 3000);
            //         }
            //     })
            //     .catch(err => {
            //         console.log(err);
            //     });
            // eslint-disable-next-line
            // this.exportExpireTime = this.formatDate(new Date().getTime() + 24 * 60 * 60 * 1000 * 3, 'YYYY-mm-dd');
        },
        formatDate(time, formatter) {
            let fmt = formatter;
            let ret;
            const date = new Date(time);
            const opt = {
                // 
                'Y+': date.getFullYear()
                    .toString(),
                // 
                'm+': (date.getMonth() + 1).toString(),
                // 
                'd+': date.getDate()
                    .toString(),
                // 
                'H+': date.getHours()
                    .toString(),
                // 
                'M+': date.getMinutes()
                    .toString(),
                // 
                'S+': date.getSeconds()
                    .toString()
                // 
            };
            for (const k in opt) {
                ret = new RegExp(`(${k})`).exec(fmt);
                if (ret) {
                    fmt = fmt.replace(ret[1], (ret[1].length === 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, '0')));
                }
            }
            return fmt;
        },
        // 
        // onOkTip() {
        //     this.$refs.modalExportTip.close();
        //     this.$refs.modalExport.open();
        // },
        // 
        onOkExport() {
            const _this = this;
            _this.$refs.exportForm.validate().then(function () {
            // 
                _this.$AjaxLoading.start();
                const ajaxExportsOptions = {
                    url: window.fdConfig.url.exports,
                    data: {
                        id: window.fdConfig.jbxxbhId,
                        type: _this.exportType,
                        password: _this.exportPassword
                    },
                    method: window.fdConfig.methodPost
                };
                _this.requestExportList(ajaxExportsOptions)
                    .then(data => {
                        if (data.code === '200') {
                            const exportObject = {
                                filePath: data.filePath,
                                fileName: data.fileName
                            };
                            _this.emitGlobalEvent(() => {
                            // 
                                _this.globalBus.$emit('global-export-main-operate', exportObject);
                            });
                            _this.$AjaxLoading.finish();
                        }
                    });
                _this.exportPassword = '';
                _this.$refs.modalExport.close();
            });
        },
        // 
        onCancel() {
            this.exportPassword = '';
            this.$refs.exportForm.clearValidateInfo();
        },
        onClose() {
            this.exportPassword = '';
            this.$refs.exportForm.clearValidateInfo();
        }
    }
};




