/**
 *@version 1.0.1
 *@author wuwg
 *@createTime 2020/03/08 - 10:57
 *@updateTime 2020/03/08 - 10:57
 *@see [jsDoc]{@link  http://www.dba.cn/book/jsdoc/JSDOCKuaiBiaoQianBLOCKTAGS/CONSTRUCTS.html}
 @description tab  
 @example
 tabList 
 ```
 [{
    key: 'all',
    count: 999,
    value: ''
}]
 ```
 //  tabList list
 <cc-tab v-model="activeKey"  :tabList="tabList"></cc-tab>
 // 
 <cc-tab v-model="activeKey"  :tabList="tabList" @select="selectTab"></cc-tab>
 // tab  tab
 <cc-tab v-slot="props"><span v-text="props.item.value"></span></cc-tab>
 */
// 
export default {
    model: {
        // key
        prop: 'activeKey',
        event: 'changeTab'
    },
    props: {
        // lable
        dKey: {
            type: String,
            default: 'key'
        },
        // 
        dName: {
            type: String,
            default: 'name'
        },
        // 
        dShow: {
            type: String,
            default: 'show'
        },
        // 
        dSize: {
            type: String,
            default: 'size'
        },
        // key
        activeKey: {type: [String, Number]},
        // 
        tabList: {
            type: Array,
            default() {
                return [
                    {
                        key: 'all',
                        name: ''
                    },
                    {
                        key: 'ml',
                        name: ''
                    },
                    {
                        key: 'nr',
                        name: ''
                    },
                    {
                        key: 'pz',
                        name: ''
                    }
                ];
            }
        }
    },
    watch: {
        // key
        activeKey: {
            handler(newValue) {
                // key
                if (this.currentKey !== newValue) {
                    // key
                    this.setActiveKey(newValue);
                }
            },
            // 
            immediate: true
        }
    },
    data() {
        return {
            // key
            currentKey: '',
            // 
            activeItem: {}
        };
    },
    // 
    methods: {

        /**
         * @function
         * @memberof module:tab
         * @description 
         * @return {undefined} 
         */
        // tab
        clickTabItem(item) {
            if (this.activeItem.key !== item.key) {
                // 
                this.setActiveItem(item);
            }
        },
        // key
        setActiveKey(key) {
            // 
            let _activeItem = null;
            // tabList
            this.tabList.every((item) => {
                if (item.key === key) {
                    _activeItem = item;
                    return false;
                }
                return true;
            });
            // 
            if (_activeItem) {
                this.setActiveItem(_activeItem);
            } else if (!this.currentKey && this.tabList.length) {
                // 
                this.setActiveItem(this.tabList[0]);
            }
        },

        /**
         * @function
         * @memberof module:tab
         * @description  
         * @param {object} item 
         * @return {undefined} 
         */
        setActiveItem(item) {
            // 
            this.activeItem = item;
            // key
            this.setCurrentKey(this.activeItem.key);
            // 
            this.$emit('select', this.activeItem);
        },
        // key
        setCurrentKey(key) {
            // key
            this.currentKey = key;
            // 
            this.$emit('changeTab', this.currentKey);
        }
    }
};




