/**
 * @description 
 */
// vue
import Vue from 'vue';
// 
import config from '@/components/config/component-config.js';
// 

// basic start
import empty from './common/empty/index.vue';
// basic end


// other end
const components = {empty};

// 
const install = function () {
    if (install.installed) {
        return false;
    }
    let i = 0;
    window.console.log('\n');
    window.console.log('\n');
    window.console.log('\n');
    // 
    Object.keys(components)
        .forEach(key => {
            i++;
            const pattern = /([A-Z])|^(\$)/gm;
            let _key = key;
            // -
            _key = _key.replace(pattern, function (a) {
                // ==$ $
                return a === '$' ? '' : `-${a.toLowerCase()}`;
            });
            // 
            Vue.component(`${config.componentAcPrefix}-${_key}`, components[key]);
            window.console.log(`${config.componentAcPrefix}-${_key}`);
        });
    window.console.log(`${i}`);
};
const API = {install};
Vue.use(API);



