/**
 * #info
 *@author    wuwg
 *@createTime   2019/05/06
 *@updateTime   2019/05/06
 *@description  tree  component ,
 */
export default {
    props: {
        // 
        calcPage: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            //  ()
            calcPagesList: {},
            // 
            caclCurrentPagesList: [],
            // 
            caclCurrentPagesListLen: 0,
            // 
            caclCurrentPage: 0,
            // 
            caclCurrentTotal: 0,
            // 
            caclCurrentBooklet: [],
            // id
            caclCurrentBookletId: 0,
            // 
            caclDossierTotal: 0
        };
    },
    methods: {
        // pageInfo
        calcChangeTargetParentPageInfo(dataItemP) {
            // 
            if (this.calcPage) {
                // index
                if (!dataItemP) {
                    return false;
                }
                // pageInfo
                switch (dataItemP.nodeType) {
                    // ()
                    case 'dossier':
                        dataItemP.pageInfo = `${dataItemP[this.dChildren].length}`;
                        break;
                    // 
                    case 'volume':
                        dataItemP.pageInfo = `${dataItemP[this.dChildren].length}`;
                        break;
                    // 
                    case 'booklet':
                        // 
                        dataItemP.pageInfo = `${dataItemP[this.dChildren]
                            .reduce((a, b) => {
                                // type 
                                if (b.nodeType === 'directory') {
                                    const _pageInfo = b.pageInfo ? parseInt(b.pageInfo, 10) : 0;
                                    return a + _pageInfo;
                                }
                                return a;
                            }, 0)}`;
                        break;
                    // 
                    case 'directory':
                    case 'sourceofmaterial':
                    case 'evidencesource':
                        // 
                        if (dataItemP.countPage) {
                            if (!dataItemP[this.dChildren].length) {
                                dataItemP.pageInfo = '0';
                            } else {
                                // 
                                dataItemP.pageInfo = `${dataItemP[this.dChildren]
                                    .reduce((a, b) => {
                                        let _value = '';
                                        // type 
                                        switch (b.nodeType) {
                                            //  || 
                                            case 'dozenmaterial':
                                            case 'directory':
                                            case 'sourceofmaterial':
                                            case 'evidencesource': // eslint-disable-line
                                                const _pageInfo = b.pageInfo ? parseInt(b.pageInfo, 10) : 0; // eslint-disable-line
                                                _value = a + _pageInfo;
                                                break;
                                            // 
                                            case 'material':
                                            case 'catalogmaterial':
                                                _value = a + b.caclCurrentPages;
                                                break;
                                            default :
                                                _value = a;
                                                break;
                                        }
                                        return _value;
                                    }, 0)}`;
                            }
                        }
                        break;
                    // (), 
                    case 'dozenmaterial':
                        if (!dataItemP[this.dChildren].length) {
                            dataItemP.pageInfo = '0';
                        } else {
                            dataItemP.pageInfo = `${dataItemP[this.dChildren].reduce((a, b) => a + b.caclCurrentPages, 0)}`;
                        }
                        break;
                    // ()
                    case 'material':
                        break;
                    default:
                        break;
                }
            }
        },
        // ,
        calcSingleRecursionBooklet(dataItem) {
            // 
            if (this.calcPage) {
                // ,
                // if (/^booklet$/igm.test(dataItem.nodeType)) {
                // 
                this.caclCurrentTotal = 0;
                // 
                this.calcPagesList[`${dataItem.id}booklet`] = [];
                // 
                this.caclCurrentBooklet = this.calcPagesList[`${dataItem.id}booklet`];
                // id
                this.caclCurrentBookletId = `${dataItem.id}booklet`;
                //   }
            }
        },
        // id
        calcSingleRecursionSetBookletId(dataItem) {
            // 
            if (this.calcPage) {
                // , bookletIdid
                if (/^dozenmaterial$|^directory$|^sourceofmaterial$|^evidencesource$|^material|^catalogmaterial$/igm.test(dataItem.nodeType)) {
                    // id
                    dataItem.bookletId = this.caclCurrentBookletId;
                }
            }
        },
        // 
        calcSingleRecursionDirectory(dataItem, dataItemP) {
            // 
            if (this.calcPage) {
                //  &&
                // 
                if ((/^directory$/igm).test(dataItem.nodeType)) {
                    // countPage
                    // 1
                    // 1
                    if (dataItemP && typeof dataItemP.countPage === 'boolean') {
                        // code = 1 code = 2 important 
                        dataItem.countPage = dataItemP.countPage ? (!dataItem.pagination) : dataItemP.countPage;
                    } else {
                        // code = 1 code = 2  important 
                        dataItem.countPage = !dataItem.pagination;
                    }
                    // 
                    if (dataItem.countPage) {
                        // 0
                        dataItem.pageInfo = '0';
                    } else {
                        // ""
                        dataItem.pageInfo = '';
                    }
                }
            }
        },
        // 
        calcSingleRecursionDozenmaterial(dataItem, dataItemP) {
            // 
            if (this.calcPage) {
                // 
                if (/^dozenmaterial$/igm.test(dataItem.nodeType)) {
                    //   important 
                    dataItem.countPage = dataItemP.countPage;
                    // 0
                    dataItem.pageInfo = '0';
                }
            }
        },
        // 
        calcSingleRecursionMaterial(dataItem, dataItemP) {
            // 
            if (this.calcPage) {
                // pdf
                if (/^material$|^catalogmaterial/igm.test(dataItem.nodeType)) {
                    //   important 
                    dataItem.countPage = dataItemP.countPage;
                    // || 
                    if ((dataItem.type === 10) || !dataItem.countPage) {// eslint-disable-line
                        // pageInfo
                        dataItem.pageInfo = '';
                        // 
                        dataItem.caclCurrentPages = 0;
                    } else {
                        //  null 
                        if (!dataItem.pages) {
                            dataItem.pages = 1;
                        }
                        // 
                        const _pages = parseInt(dataItem.pages, 10);// eslint-disable-line
                        // 
                        const _prevPages = this.caclCurrentTotal + 1;
                        // 
                        this.caclCurrentTotal += _pages;
                        // 
                        this.caclDossierTotal += _pages;
                        // pageInfo  + ''
                        dataItem.pageInfo = _pages > 1 ? `${_prevPages}-${this.caclCurrentTotal}` : `${this.caclCurrentTotal}`;
                        dataItem.pageStart = _pages > 1 ? _prevPages : this.caclCurrentTotal;
                        // 
                        dataItem.caclCurrentPages = _pages;
                    }
                    // ~
                    if (dataItem.type !== 10) {// eslint-disable-line
                        // 
                        this.caclCurrentBooklet[this.caclCurrentBooklet.length] = (dataItem);
                    }
                }
            }
        },
        // 
        calcSingleRecursion(dataItem, dataItemP) {
            // 
            if (this.calcPage) {
                // id
                this.calcSingleRecursionSetBookletId(dataItem, dataItemP);
                // 
                switch (true) {
                    //  id
                    case (/^dossier/igm).test(dataItem.nodeType):
                        // 
                        this.calcSingleRecursionBooklet(dataItem, dataItemP);
                        break;
                    case (/^booklet$/igm).test(dataItem.nodeType):
                        // 
                        this.calcSingleRecursionBooklet(dataItem, dataItemP);
                        break;
                    case (/^directory$/igm).test(dataItem.nodeType):
                        // 
                        this.calcSingleRecursionDirectory(dataItem, dataItemP);
                        break;
                    case (/^dozenmaterial$/igm).test(dataItem.nodeType):
                        // 
                        this.calcSingleRecursionDozenmaterial(dataItem, dataItemP);
                        break;
                    case (/^material|^catalogmaterial$/igm).test(dataItem.nodeType):
                        // 
                        this.calcSingleRecursionMaterial(dataItem, dataItemP);
                        break;
                    default:
                        break;
                }
            }
        },

        // 
        calcResetPages() {
            // 
            if (this.calcPage) {
                // 
                this.calcPagesList = {};
                // 
                this.caclCurrentTotal = 0;
                // 
                this.caclDossierTotal = 0;
                // 
                this.caclCurrentBooklet = [];
                //  ()
                this.caclCurrentPagesList = [];
                // 
                this.caclCurrentPagesListLen = 0;
                // 
                this.caclCurrentPage = 0;
                // id
                // this.caclCurrentBookletId = '-999999';
                this.caclCurrentBookletId = '';
            }
        },

        // 
        calcPages(callback) { // eslint-disable-line
            // 
            if (this.calcPage) {

                /* @wuwg  todo  2019-05-19
                 // 
                 let _resultDataList = JSON.parse(JSON.stringify(this.dataList));
                 // 
                 this.calcResetPages();
                 // 
                 this.calcSingleRecursion(_resultDataList);
                 // 
                 this.update(_resultDataList, callback);
                 */
            }
        },

        // 
        caclSetcaclCurrentPagesList(dataItem) {
            // 
            if (this.calcPage) {
                const _data = dataItem;
                const _type = _data.nodeType;
                // id
                let _bookletId = '';
                // 
                if (/^dozenmaterial$|^directory$|^sourceofmaterial$|^evidencesource$|^material$|^catalogmaterial$/igm.test(_type)) {
                    _bookletId = _data.bookletId;
                } else if (_data.children.length > 0) {
                    // eslint-disable-next-line default-case
                    switch (_type) {
                        // 
                        case 'booklet':
                            _bookletId = _data.id;
                            break;
                        // 
                        case 'volume':
                            _bookletId = _data.children[0].id;
                            break;
                        // 
                        case 'dossier':
                            //  @todo  2019-11-12  wuwg
                            _bookletId = `${_data.id}booklet`;
                            break;
                    }
                }
                // id
                if (typeof _bookletId !== 'undefined' && _bookletId !== '') {
                    // id
                    this.caclCurrentBookletId = _bookletId;
                    // id
                    this.caclCurrentPagesList = this.calcPagesList[this.caclCurrentBookletId];
                    // 
                    this.caclCurrentPagesListLen = this.caclCurrentPagesList.length;
                }
                // 
                if (/^material|^catalogmaterial$/igm.test(_type)) {
                    // 
                    this.caclSetCurrentPage(_data.id);
                } else {
                    // 0
                    this.caclCurrentPage = 0;
                }
            }
        },

        // 
        caclSetCurrentPage(id) {
            // 
            if (this.calcPage) {
                // 
                this.caclCurrentPage = 0;
                this.caclCurrentPagesList.every((item, index) => {
                    if (item.id === id) {
                        this.caclCurrentPage = index;
                    }
                    return item.id !== id;
                });
            }
        },
        // 
        caclPrevPage() {
            // 
            if (this.calcPage) {
                // 
                if (this.caclCurrentPage !== 0) {
                    //  - 1
                    this.caclCurrentPage = this.caclCurrentPage - 1;
                    // 
                    this.caclSetActiveItem();
                }
            }
        },
        // 
        caclNextPage() {
            // 
            if (this.calcPage) {
                // 
                if (this.caclCurrentPage !== this.caclCurrentPagesListLen - 1) {
                    //  + 1
                    this.caclCurrentPage = this.caclCurrentPage + 1;
                    // 
                    this.caclSetActiveItem();
                }
            }
        },

        // 
        caclSetActiveItem() {
            // 
            if (this.calcPage) {
                // 
                const _data = this.caclCurrentPagesList[this.caclCurrentPage];
                if (_data) {
                    // 
                    // this.gSetActiveItem(_data, null, true, false);
                    this.gSetActiveId(_data.id);
                }
            }
        },

        // 
        caclGotoTargetByYm(page, callback) { // eslint-disable-line
            // 
        //     if (this.calcPage) {
        //         // 
        //         const _targetPage = parseInt(page);
        //         // 
        //         let _targetData = null;
        //         //  || 
        //         let _overtopYm = 0;
        //         // 
        //         // 
        //         const _isNotFind = this.caclCurrentPagesList.every((item) => {
        //             let _flag = true;
        //             // 
        //             if (item.code === 1) {
        //                 // 
        //                 if (typeof (item.pageInfo) === 'string') {
        //                     // 
        //                     const _pageInfo = item.pageInfo.split('-').map((itemC) => parseInt(itemC));
        //                     // 
        //                     if (_pageInfo[0] <= _targetPage && _targetPage <= _pageInfo[1]) {
        //                         _flag = false;
        //                         _targetData = item;
        //                         _overtopYm = _targetPage - _pageInfo[0];
        //                     }
        //                     // 
        //                 } else {
        //                     // 
        //                     if (parseInt(item.pageInfo) === _targetPage) {
        //                         _flag = false;
        //                         _targetData = item;
        //                     }
        //                 }
        //             }
        //             return _flag;
        //         });
        //         // 
        //         if (_isNotFind) {
        //             // _overtopYm = fdEventBus.getAppCommonPdfControllerMaxPage();
        //         }
        //         // 
        //         // callback(_targetData, _overtopYm);
        //     }
        }
    }
};



