export default {
    data() {
        return {routerQuery: {}};
    },
    methods: {
        // 1. 
        changeMainRouter(data, forceUpdate) {
            const treeItem = data.treeItem;
            // id
            if (this.$route.params.id !== treeItem.code || forceUpdate) {
                // 1.
                // this.breadSetList(this.getBreadList(1));
                this.breadSetList([{
                    // 
                    ext: {type: 'tree'},
                    // 
                    name: treeItem.name
                }]);
                // path
                const _path = `/spgl/main/${treeItem.code}`;
                // querynav
                const _navExt = encodeURIComponent(JSON.stringify([{
                    // 
                    ext: {type: 'tree'},
                    // 
                    name: treeItem.name
                }]));
                // 
                if (!(_path === this.$route.path && this.$route.query.nav)) {
                    // 2. 
                    this.$router.push({
                        path: _path,
                        query: {
                            // 
                            nav: _navExt
                        }
                    });
                }
                // 3.
                this.setCurrentComponent(treeItem.code);
            }
        },
        // 2. 
        changeGoDownRouter(dataType, data, pData, index) {
            // 1. 
            let _navItem = this.getTargetNavExtItem(dataType, data, pData, index);
            // 2. 
            const _name = this.getBreadListItemName(dataType, data, pData);
            // 3. 
            _navItem = {
                name: _name,
                ext: _navItem
            };
            // 4. 
            this.breadSetList(this.breadList.concat(_navItem));
            // 5. 
            this.$router.push({
                path: this.$route.path,
                query: {nav: encodeURIComponent(JSON.stringify(this.breadList))}
            });
            // 6. 
            this.setCurrentComponent();
        },
        // 
        getTargetNavExtItem(dataType, data, pData, index) {
            // 
            let _targetNavItem = {};
            // 
            const _targetPreNavItem = this.breadList.slice(this.breadList.length - 1)[0].ext;
            //  queryData
            switch (dataType) {
                // form-table 
                case 'form-table':
                    _targetNavItem = {
                        // 
                        ..._targetPreNavItem,
                        // 
                        index: data.index,
                        // layoutId
                        layoutId: data.layoutId,
                        // id
                        id: pData.id,
                        // treetableform-table
                        type: 'form-table',
                        clickIndex: index
                    };
                    break;
                // table 
                case 'table':
                    _targetNavItem = {
                        // 
                        ..._targetPreNavItem,
                        // layoutId
                        layoutId: data.layoutId,
                        // trId
                        trId: pData.id,
                        // treetableform-table
                        type: 'table',
                        // 
                        cxdh: typeof _targetPreNavItem.cxdh === 'undefined' ? pData.id : _targetPreNavItem.cxdh
                    };
                    break;
                default:
                    break;
            }
            return _targetNavItem;
        },

        // 
        getBreadListItemName(dataType, dataItem, pDataItem) {
            let _name = '';
            switch (dataType) {
                // 
                case 1:
                case '1':
                case 'tree':
                    break;
                case 2: // eslint-disable-line
                    _name = dataItem.name;
                    break;
                case 3:  // eslint-disable-line
                    _name = dataItem.typeName;
                    break;
                case 'table':
                    if (!dataItem.key) {
                        window.console.error('key');
                    }
                    _name = dataItem.key ? dataItem.key : dataItem.name;
                    break;
                case 'form-table':
                    if (!dataItem.label) {
                        window.console.error('label');
                    }
                    _name = dataItem.label ? dataItem.label : pDataItem.label;
                    break;
                default:
                    break;
            }
            return _name;
        }
    }
    // beforeRouteEnter(to, from, next) {
    //     debugger
    //     //  confirm 
    //     //  `this`  !important
    //     // 
    //     next((vm) => {
    //         //  `vm` 
    //     });
    // },
    // beforeRouteUpdate(to, from, next) {
    //     debugger
    //     // 
    //     //  /foo/:id /foo/1  /foo/2 
    //     //  Foo 
    //     // console.log(to)
    //     //  `this`  !important
    //     next();
    //     // 
    //     console.log('to1',to);
    //     // this.breadSetList(1);
    //     // this.globalBus.$emit('global-change-tree-active', this.$route.params.id);
    //     // 
    //     // this.setCurrentComponent(this.$route.params.id, this.pId);
    // },
    // beforeRouteLeave(to, from, next) {
    //     debugger
    //     // 
    //     //  `this`  !important
    //     next();
    // }
};

/**
 
  beforeRouteLeave 
  beforeEach 
  beforeRouteUpdate  (2.2+)
  beforeEnter
 
  beforeRouteEnter
  beforeResolve  (2.5+)
 
  afterEach 
  DOM 
  beforeRouteEnter  next 
 */



