// 
import dragAddPlaceholder from './dragAddPlaceholder.js';
import dropHandler from './dropHandler.js';

export default {
    mixins: [
        dragAddPlaceholder,
        dropHandler
    ],
    data() {
        return {
            // 
            edCurrentDraggable: true
        };
    },
    methods: {
        // 
        edSetCurrentDraggable(flag) {
            this.edCurrentDraggable = flag;
        },
        // 
        edResetOriginData() {
            // 1
            const _currentDataList = JSON.parse(JSON.stringify(this.edCopyCurrentDataList));
            // 2 
            this.setCurrentDataList(_currentDataList);
        },

        // 1.
        edDragstartHandler(dataItem, event, isOutSide = false) {
            // 1.
            // this.edSetCopyCurrentDataList();
            // 2.()
            this.edSetDragging(true);
            // 3.
            this.edSetCurrentDragDataItem(dataItem, isOutSide);
            // 4.
            this.edBindDragEvent();
            // 5.,
            // this.$emit('dragstart', dataItem, event, this);
            // originTree 2019-05-21
            // 6.1
            this.setDragNodeDescendantState();
            // 6.2 
            this.setTreeDragContent();
            // 6.3
            this.setTreeDragContentStyle(event, true);
            // 6.4 
            this.edSetCopyCurrentDataList();
            // 6.5tree
            this.emitEvent('dragstart', event, this.dragNode);
            // 
            window.treeCacheObject.contentPosition = {
                startTop: event.pageY,
                startLeft: event.pageX
            };
            // 
            window.fdGlobal.addClass(window.fdGlobal.body, 'fd-user-select-none');
            window.fdGlobal.addClass(window.fdGlobal.body, 'fd-dragging');
        },
        // 2.()
        edDragHandler(event) {
            // 1.
            this.edSetDragPreviewPosition(event);
            // 2.
            this.edSetDragPreviewStatus(true);
            // 3.,
            // this.$emit('drag', event, this);
            //  originTree 2019-05-21
            // 
            this.emitEvent('drag', event, this.dragNode);
        },
        // 3.()
        edDragendHandler(event) {
            // 1.
            this.edClearMouseDragStatus();
            // 2.
            this.edResetOriginData();
            // 
            //  originTree 2019-05-21
            this.emitEvent('dragend', event, this);
            // 3.,
            // this.$emit('dragend', event, this);
        },

        // 4.()
        edDragenterHandler(event, dataItem, index) {
            // 1.
            this.edSetCurrentDropDataItem(dataItem);
            // 2.
            this.edAddPlaceholder(event, dataItem, index);
            // 3., @todo   this.hoverNode
            this.emitEvent('dragenter', event, this.hoverNode);
        },

        // 5.()
        edDragoverHandler(event, dataItem, index) {
            // 1.
            this.edSetCurrentDropDataItem(dataItem);
            // 2.
            this.edAddPlaceholder(event, dataItem, index);
            // 3.,  @todo   this.hoverNode
            this.emitEvent('dragover', event, this.hoverNode);
        },

        // 6.()
        edDragleaveHandler(event) {
            // 1.
            this.edSetCurrentDropDataItem(null);
            // 2.
            this.edSetDropPostion('');
            // 3.
            this.edResetOriginData();
            // 1.
            this.emitEvent('dragleave', event, null);
        },
        // 7.()
        edDropHandler(dataItem) {
            const _this = this;
            // hoverNode by xieyq 20190620 dropdrophoverNodemouseoverhoverNode~
            if (dataItem.type !== 'placeholder') {
                // 1.  -- 
                this.edSetCurrentDropDataItem(dataItem);
            }
            // 
            const _edCurrentDragDataItem = JSON.parse(JSON.stringify(_this.edCurrentDragDataItem));
            // const _edCurrentDropDataItem = JSON.parse(JSON.stringify(_this.edCurrentDropDataItem));
            // mouseup
            _this.edClearDragRelativeData();
            // 
            _this.edDropHandlerCallbackSuccess = () => {
                // 1.
                this.gSetActiveItem(_edCurrentDragDataItem, null, true);
                // 2.dataList
                _this.edOperateOriginDataList();
                // 3.
                _this.dTransformTreeData();
                // 4.
                _this.eUpdateView();
                // 6. 
                _this.edClearMouseDragStatus();
                // null
                _this.edDropHandlerCallbackSuccess = null;
            };
            // 
            _this.edDropHandlerCallbackError = () => {
                // 1.,
                _this.$emit('drop', null, null, event, _this);
                // 2. 
                _this.edClearMouseDragStatus();
                // 3.
                _this.edResetOriginData();
                _this.edDropHandlerCallbackError = null;
            };
            //   console.log('dropdragend')
            // ,
            if (this.allowDrag) {
                const _allowPosition = this.allowPosition(this.hoverNode, this.dragNode);
                const _allowPositionLen = _allowPosition.length;
                let _flag = false;
                for (let i = 0; i < _allowPositionLen; i++) {
                    if (this.targetPostion === _allowPosition[i]) {
                        _flag = true;
                    }
                }
                // 
                if (this.allowDrop(this.hoverNode, this.dragNode) && _flag) {
                    // 
                    this.emitEvent('drop', event, this);
                    // 
                    this.moveData();
                    // this.$Message.success('');
                } else {
                    // 2019/7/25  
                    // this.$Message.error('')
                    this.$Message.error('');
                    this.edDropHandlerCallbackError();
                }
                // 
                this.edClearMouseDragStatus();
            } else {
                this.$Message.error('');
                this.edDropHandlerCallbackError();
            }
        },
        // 
        edClearDragRelativeData() {
            // 
            this.edClearMouseTimer();
            // ()
            this.edSetDragging(false);
            // 
            this.edUnbindDragEvent();
            // 
            this.edSetDragPreviewStatus(false);
            // id
            this.edSetDropInnerItemId('');
            // mouseup
            this.edClearMouseUpTimer();
        },
        // 7.()
        // originTree 2019-05-21
        edDropHandlerGood(event) {
            const _this = this;
            // 
            const _edCurrentDragDataItem = JSON.parse(JSON.stringify(_this.edCurrentDragDataItem));
            const _edCurrentDropDataItem = JSON.parse(JSON.stringify(_this.edCurrentDropDataItem));
            // 1.
            // mouseup
            _this.edClearDragRelativeData();
            // 
            _this.edDropHandlerCallbackSuccessClearStatus = () => {
                // 1.
                this.gSetActiveItem(_edCurrentDragDataItem, null, false);
                // 2.,
                _this.$emit('drop', _edCurrentDragDataItem, _edCurrentDropDataItem, event, _this);
                // 3. 
                _this.edClearMouseDragStatus();
            };
            // 
            _this.edDropHandlerCallbackSuccess = () => {
                // 1.
                this.gSetActiveItem(_edCurrentDragDataItem, null, false);
                // 2.dataList
                _this.edOperateOriginDataList();
                // 3.
                _this.dTransformTreeData();
                // 4.
                _this.eUpdateView();
                // 5.,
                _this.$emit('drop', _edCurrentDragDataItem, _edCurrentDropDataItem, event, _this);
                // 6. 
                _this.edClearMouseDragStatus();
            };
            // 
            _this.edDropHandlerCallbackError = () => {
                // 1.,
                _this.$emit('drop', null, null, event, _this);
                // 2. 
                _this.edClearMouseDragStatus();
                // 3.
                _this.edResetOriginData();
            };
            // 
            const _edDropHandlerCallback = _this.edDropHandlerCallback(_edCurrentDragDataItem, _edCurrentDropDataItem, _this.edDropPostion, _this.edMultiple, event, _this);
            if (_edDropHandlerCallback) {
                _edDropHandlerCallback.then(() => {
                    _this.edDropHandlerCallbackSuccess();
                }, () => {
                    _this.edDropHandlerCallbackError();
                });
            }
        }
    }
};




