/**
 * @version 20190401
 * @author wuwg
 * @createTime  20190409
 * @updateTime  202001125
 * @namespace  eToggle
 * @decription  
 * 202001125 :
 *  - 
 *  -  eToggleOperateHandler 
 */
export default {
    methods: {
        // 
        eToggleHandler(dataItem) {
            const _targetItem = this.cGetCache(dataItem[this.dKey]);
            //   
            if (this.asyncNodeData && !_targetItem[this.dChildren].length) {
                // 1. 
                dataItem.asyncNodeDataFlag = true;
                // 2.
                _targetItem.asyncNodeDataFlag = true;
                // 3. 
                this.asyncNodeDataHandlerCallback(dataItem).then((dataList) => {
                    //  /
                    this.eToggleOperateHandler(dataItem);
                    if (Array.isArray(dataList) && dataList.length) {
                        // 
                        _targetItem.children = dataList;
                        // 
                        this.refresh();
                    } else {
                        // 2.
                        this.eScroll();
                    }
                })
                // 
                    .finally(() => {
                        // 1.
                        dataItem.asyncNodeDataFlag = false;
                        // 2.
                        _targetItem.asyncNodeDataFlag = false;
                    });
            } else {
                //  1./
                this.eToggleOperateHandler(dataItem);
                // 2.
                this.eScroll();
            }
        },
        // /
        eToggleOperateHandler(dataItem) {
            // 1.
            dataItem.extend = !dataItem.extend;
            // 2.
            this.cUpdateCache(dataItem, 'extend');
            // 3.
            // this.eToggleOperateChildren(dataItem);
            // 4.
            if (dataItem.extend) {
                this.eToggleExtendHandler(dataItem);
            } else {
                this.eToggleCollpaseHandler(dataItem);
            }
        },

        /**
         * @param {object} dataItem 
         * @description  extend
         * @returns {*} 
         */
        eToggleExtendHandler(dataItem) {
            // 
            this.emitEvent('extend', null, dataItem);
        },

        /**
         * @param {object} dataItem 
         * @description  collapse
         * @returns {*} 
         */
        eToggleCollpaseHandler(dataItem) {
            // 
            this.emitEvent('collapse', null, dataItem);
        },
        // ,
        eToggleOperateChildren(dataItem) {
            // 
            if (Array.isArray(dataItem[this.dChildren])) {
                const _showFlag = dataItem.extend;
                // 
                dataItem[this.dChildren].forEach((item) => {
                    // 
                    this.eToggleSingleRecursion(item, _showFlag);
                });
            }
        },

        /**
         *
         * @param {object} dataItem 
         * @param {boolean} showFlag 
         * @description 
         * @returns {*} 
         */
        eToggleSingleRecursion(dataItem, showFlag) {
            // 
            dataItem.show = showFlag;
            // 
            this.cUpdateCache(dataItem, 'show');
            // 
            if (dataItem.hasOwnProperty(this.dChildren)) {
                // 
                dataItem[this.dChildren].forEach((item) => {
                    // 
                    this.eToggleSingleRecursion(item, showFlag);
                });
            }
        }
    }
};



