/**
 * @author    wuwg
 * @createTime    2020-09-16
 * @updataTime    2020-09-16
 * @description   form-table 
 */
import mockData from './mockData.js';
// 
const NUM_2 = 2;
const NUM_6 = 6;
const NUM_10 = 10;

function getRandomId() {
    return (Math.random() * Math.pow(NUM_10, NUM_6)).toFixed(NUM_2);
}

// 
const module = {
    props: {
        // lable
        dLable: {
            type: String,
            default: 'label'
        },
        // 
        dValue: {
            type: String,
            default: 'value'
        },
        // 
        dCol: {
            type: String,
            default: 'col'
        },
        // 
        dType: {
            type: String,
            default: 'type'
        },
        // ,
        dRow: {
            type: String,
            default: 'row'
        },
        // table()
        dataList: {
            type: Array,
            default() {
                // return mockData;
                // mockData
                return [];
            }
        },
        // , 
        totalCol: {
            type: Number,
            default: 6,
            // 
            validator(value) {
                return value % NUM_2 === 0;
            }
        }
    },
    data() {
        return {
            // 
            tableData: []
        };
    },
    watch: {
        // 
        totalCol() {
            if (Array.isArray(this.originDataList)) {
                this.update(this.originDataList);
            }
        }
    },
    created() {
        // 
        this.init();
    },
    methods: {
        // 
        init() {
            // 
            this.update(this.dataList);
        },
        // table
        setTableData(_dataList) {
            this.tableData = _dataList;
        },
        // 
        update(dataList, callback) {
            //  
            if (Array.isArray(dataList)) {
                // 
                this.originDataList = JSON.parse(JSON.stringify(dataList));
                // 
                const _tableDataList = this.transformDataList(dataList);
                // table
                this.setTableData(_tableDataList);
            }
            // 
            if (typeof callback === 'function') {
                this.$nextTick(() => {
                    callback();
                });
            }
        },
        // 
        getRowColObject(totalCol) {
            const rowColObject = {};
            // 
            for (let i = 0; i < totalCol / NUM_2; i++) {
                //  0
                rowColObject[`${i + 1}colResidueRow`] = 0;
            }
            return rowColObject;
        },
        // 
        setRowColObject(rowCount, colCount, rowColObject) {
            //    !important20200924
            // 
            let _currentCol = 0;
            // 
            this.currentRowDataList.forEach((item) => {
                _currentCol = _currentCol + item[this.dCol];
            });
            // 
            _currentCol = _currentCol / NUM_2;
            // 
            for (let i = 0; i < colCount / NUM_2; i++) {
                rowColObject[`${_currentCol + i + 1}colResidueRow`] = rowCount;
            }
        },
        // 
        setValidColDataList(rowColObject) {
            const validColDataList = [];
            // 
            for (const _name in rowColObject) {
                if (rowColObject[_name] === 0) {
                    validColDataList.push(parseInt(_name, 10));
                }
            }
            // 
            this.validColDataList = validColDataList;
        },
        // 1
        declineRowColObject(rowColObject) {
            // 
            for (const _name in rowColObject) {
                if (rowColObject[_name] !== 0) {
                    rowColObject[_name] = rowColObject[_name] - 1;
                }
            }
        },
        // 
        adjustAllRowSpan(rowColObject) {
            // 
            let _allRowSpan = true;
            // 
            for (const _name in rowColObject) {
                // 0
                if (rowColObject[_name] === 0) {
                    _allRowSpan = false;
                    // 
                    break;
                }
            }
            return _allRowSpan;
        },
        // 
        getCurrentRowTotalCol(rowColObject, totalCol) {
            let _residueRow = 0;
            // 
            for (const _name in rowColObject) {
                // 0
                if (rowColObject[_name] !== 0) {
                    // 
                    _residueRow = _residueRow + 1;
                }
            }
            // 
            return totalCol - _residueRow * NUM_2;
        },
        // 
        resetCurrentCountVariable() {
            // 0
            this.currentCol = 0;
            //  
            this.currentRowDataList = [];
        },
        // 
        resetCurrentRowTotalCol(_rowColObject) {
            //   
            this.currentRowTotalCol = this.getCurrentRowTotalCol(_rowColObject, this.totalCol);
        },
        // 
        addRowDataToResultDataList(rowDataList, resultDataList, rowColObject) {
            // 
            resultDataList.push(rowDataList);
            // 1
            this.declineRowColObject(rowColObject);
            // 
            let _allRowSpan = this.adjustAllRowSpan(rowColObject);
            // tr !important, tr @author wuwg
            while (_allRowSpan) {
                // 
                resultDataList.push([]);
                // 1
                this.declineRowColObject(rowColObject);
                // 
                _allRowSpan = this.adjustAllRowSpan(rowColObject);
            }
            // 
            this.setValidColDataList(rowColObject);
            //  
            this.resetCurrentRowTotalCol(rowColObject);
        },
        // 
        addDataToResidueDataList(residueDataList, dataItem) {
            // 
            residueDataList.push(dataItem);
        },
        // 
        addDataToCurrentRowDataList(dataItem) {
            // 
            this.currentRowDataList.push(dataItem);
        },

        //  
        adjustDataItemCanAddToCurrentRowDataList(dataItem) {
            let _flag = true;
            //  
            const _dataItemCol = dataItem[this.dCol] / NUM_2;
            // 
            let _currentCol = 0;
            // 
            // 
            this.currentRowDataList.forEach((item) => {
                _currentCol = item[this.dCol] + _currentCol;
            });
            //  
            const _validColDataList = this.validColDataList;
            // 
            let _currentCount = _validColDataList[_currentCol];
            //  
            for (let i = _currentCol + 1; i < _dataItemCol; i++) {
                _currentCount = _currentCount + 1;
                //  
                if (_currentCount !== _validColDataList[i]) {
                    _flag = false;
                    // 
                    break;
                }
            }
            return _flag;
        },

        // 
        transformDataList(dataList) {
            //  
            if (Array.isArray(dataList) && dataList.length) {
                // 
                const _totalCol = this.totalCol;

                // 
                let _dataObject = {
                    // 1. 
                    resultDataList: [],
                    // 2. 
                    residueDataList: [],
                    // 3.   
                    imgList: [],
                    //  
                    rowColObject: this.getRowColObject(_totalCol)
                };
                //   
                this.currentRowTotalCol = this.totalCol;
                // 
                this.setValidColDataList(_dataObject.rowColObject);
                // 
                this.resetCurrentCountVariable();
                //  1. 
                let {
                    flag, currentDataList
                } = this.loopOperateDataList(dataList, _dataObject);
                // 
                while (!flag) {
                    // 
                    const obj = this.loopOperateDataList(currentDataList, _dataObject);
                    // 
                    flag = obj.flag;
                    // 
                    currentDataList = obj.currentDataList;
                }
                // 
                // if (_dataObject.currentRowDataList.length) {
                //     // unshift,
                //     _dataObject.residueDataList.unshift(_dataObject.currentRowDataList);
                // }
                // 1. 
                this.operateResultResidueData(currentDataList, _dataObject);
                // 2. 
                this.operateImgList(_dataObject.imgList, _dataObject.resultDataList);
                // 
                const _resultDataList = _dataObject.resultDataList;
                // 
                _dataObject = null;
                // 
                return _resultDataList;
            }
            return [];
        },

        // 
        loopOperateDataList(dataList, dataObject) {
            let _dataList = dataList;
            // 
            const {
                // 1. 
                resultDataList,
                //  2. 
                residueDataList,
                // 3.   
                imgList,
                //  
                rowColObject
            } = dataObject;

            // 
            const flag = _dataList.every((item, index) => {
                // 
                const _flag = this.operateDataItem(item, residueDataList, resultDataList, imgList, this.currentRowTotalCol, rowColObject);
                // 
                if (!_flag) {
                    // 
                    _dataList = dataList.splice(index + 1);
                    //   
                    if (residueDataList.length) {
                        //  + 
                        _dataList = residueDataList.concat(_dataList);
                        // 
                        dataObject.residueDataList = [];
                    }
                }
                return _flag;
            });
            return {
                flag: flag,
                currentDataList: _dataList
            };
        },

        /**
         * @description 
         * @param {Object} dataItem 
         * @param {Array} residueDataList  
         * @param {Array} resultDataList   
         * @param {Array} imgList   
         * @param {Number} totalCol 
         * @param {Object} rowColObject 
         * @returns {void}  
         */
        operateDataItem(dataItem, residueDataList, resultDataList, imgList, totalCol, rowColObject) {
            // id
            dataItem.labelId = getRandomId();
            dataItem.valueId = getRandomId();
            let _flag = true;
            // console.log(totalCol);
            //  important!
            if (dataItem[this.dType] === 'img') {
                imgList.push(dataItem);
                return false;
            }
            // 
            //  
            let _dataItemCol = parseInt(dataItem[this.dCol], 10);
            //  
            _dataItemCol = _dataItemCol || NUM_2;
            //  
            const _dataItemRow = parseInt(dataItem[this.dRow], 10) || 1;
            // 
            dataItem[this.dCol] = _dataItemCol;
            //  
            dataItem[this.dRow] = _dataItemRow;
            // 1. 
            if (_dataItemCol >= this.totalCol) {
                // 
                dataItem[this.dCol] = this.totalCol;
                // 
                if (!this.currentRowDataList.length) {
                    // 1  
                    if (_dataItemRow !== 1) {
                        // 
                        this.setRowColObject(_dataItemRow, _dataItemCol, rowColObject);
                    }
                    // 
                    this.addRowDataToResultDataList([dataItem], resultDataList, rowColObject);
                    //   
                    _flag = false;
                } else {
                    // 
                    this.addDataToResidueDataList(residueDataList, dataItem);
                }
            } else {
                // 2. 
                // 2.1  
                this.currentCol = this.currentCol + _dataItemCol;
                // 2.2 
                if (this.currentCol > totalCol) {
                    // 2.2.1 
                    this.currentCol = this.currentCol - _dataItemCol;
                    // 
                    this.addDataToResidueDataList(residueDataList, dataItem);
                    // 
                } else if (this.adjustDataItemCanAddToCurrentRowDataList(dataItem)) {
                    // 2.3  
                    // 1  
                    if (_dataItemRow !== 1) {
                        // 
                        this.setRowColObject(_dataItemRow, _dataItemCol, rowColObject);
                    }
                    // 
                    if (this.currentCol === totalCol) {
                        //  
                        this.addDataToCurrentRowDataList(dataItem);
                        // 
                        this.addRowDataToResultDataList(this.currentRowDataList, resultDataList, rowColObject);
                        // 
                        this.resetCurrentCountVariable();
                        //   
                        _flag = false;
                        // 2.4  
                    } else {
                        this.addDataToCurrentRowDataList(dataItem);
                    }
                } else {
                    // 2.2.1 
                    this.currentCol = this.currentCol - _dataItemCol;
                    // 
                    this.addDataToResidueDataList(residueDataList, dataItem);
                }
            }
            return _flag;
        },

        // 
        getFillRowDataList(dataItem) {
            const _dataList = [];
            //  
            const _validColDataList = this.validColDataList;
            // console.log(_validColDataList);
            // 
            let _currentCount = 1;
            // 
            const _successionCount = [];
            const _len = _validColDataList.length;
            //  , 
            for (let i = 0; i < _len; i++) {
                //  
                // 
                if ((i !== _len - 1) && _validColDataList[i] + 1 !== _validColDataList[i + 1] || i === _len - 1) {
                    _successionCount.push(_currentCount);
                    _currentCount = 1;
                } else {
                    _currentCount = _currentCount + 1;
                }
            }
            const _maxCol = Math.max.apply(null, _successionCount);
            //  col
            dataItem[this.dCol] = _maxCol * NUM_2;
            // 
            let _flag = true;
            // 
            _successionCount.forEach((item) => {
                // 
                let _dataItem = {
                    [this.dType]: 'text',
                    [this.dLable]: '',
                    [this.dValue]: '',
                    [this.dRow]: 1,
                    labelId: getRandomId(),
                    valueId: getRandomId(),
                    [this.dCol]: item * NUM_2
                };
                // 
                if (item === _maxCol && _flag) {
                    _dataItem = dataItem;
                    // , false;
                    _flag = false;
                }
                // 
                _dataList.push(_dataItem);
                // 
                _dataItem = null;
            });
            // console.log(_dataList);
            // console.log(_successionCount);
            // console.log(_maxCol);
            return _dataList;
        },

        /**
         * @description 
         * @param {Array} residueDataList  
         * @param {Object} dataObject   
         * @returns {void}  
         */
        operateResultResidueData(residueDataList, dataObject) {
            // 
            residueDataList.forEach((item) => {
                // 
                item[this.dCol] = this.currentRowTotalCol;
                // 
                const _currentRowDataList = this.getFillRowDataList(item);
                // 
                this.addRowDataToResultDataList(_currentRowDataList, dataObject.resultDataList, dataObject.rowColObject);
            });
        },

        /**
         * @description 
         * @param {Array} imgList  
         * @param {Array} resultDataList   
         * @returns {void}  
         */
        operateImgList(imgList, resultDataList) {
            // 
            if (imgList.length > 0) {
                // 
                const _imgItem = imgList[0];
                // 
                // _resultDataList[0].unshift(_imgItem);
                // 
                resultDataList[0].push(_imgItem);
                // 
                const _imgItemRow = parseInt(_imgItem[this.dRow], 10);
                // 
                if (_imgItemRow >= resultDataList.length) {
                    _imgItem[this.dRow] = resultDataList.length;
                } else {
                    // 
                    for (let i = _imgItemRow; i < resultDataList.length; i++) {
                        // 
                        // _resultDataList[i].unshift({[this.dType]: 'placeholder'});
                        const _rowDataList = resultDataList[i];
                        // 
                        if (_rowDataList.length) {
                            // 
                            const _lastItemCol = _rowDataList[_rowDataList.length - 1][this.dCol];
                            _rowDataList[_rowDataList.length - 1][this.dCol] = _lastItemCol + 1;
                        }

                        // //   @detail    20200925 wuwg
                        // resultDataList[i].push({
                        //     [this.dType]: 'placeholder',
                        //     labelId: getRandomId(),
                        //     valueId: getRandomId()
                        // });
                    }
                }
            }
        },
        // 
        clickOperate(itemCC, item, index) {
            this.$emit('operate', itemCC, item, index);
        }
    }
};
export default module;



