/**
 * @author    wuwg
 * @createTime    2020-09-15
 * @updataTime    2020-09-15
 * @description   form-table
 */

// import mockData from './mockData.js';
// 
const module = {
    props: {
        // lable
        dTabKey: {
            type: String,
            default: 'key'
        },
        // 
        dTabName: {
            type: String,
            default: 'name'
        },
        dTabIndex: {
            type: String,
            default: 'index'
        },
        dTabSize: {
            type: String,
            default: 'size'
        },
        dTabNumberShow: {
            type: String,
            default: 'show'
        },
        // 
        dTabChildren: {
            type: String,
            default: 'children'
        },
        // 
        dCategory: {
            type: String,
            default: 'category'
        },
        // 
        dValue: {
            type: String,
            default: 'value'
        },
        // 
        dataObject: {
            type: Array,
            // 
            default() {
                return [];
            }
        }
    },
    // watch: {
    //     // 
    //     dataObject(newVal, oldVal) {
    //         if (newVal !== oldVal) {
    //             this.update(this.dataObject);
    //         }
    //     }
    // },
    data() {
        return {
            // tab 
            tabList: [],
            // tabItem
            activeItem: {},
            // 
            currentDataList: [],
            // 
            limit: window.fdConfig.limit,
            // index
            clickIndex: 0,
            dataList: []
        };
    },
    created() {
        // 
        this.bindGlobalEvent();
        // 
        this.init();
    },
    beforeDestroy() {
        // 
        this.unbindGlobalEvent();
    },
    activated() {
        this.init();
    },
    methods: {

        /**
         * @description 
         * @name init
         * @return {*} 
         */
        init() {
            // 1. 
            this.update(this.dataObject);
        },
        // 
        update(dataList, type = 'category-form-table') {
            this.dateSection = '';
            if (!(Array.isArray(dataList) && dataList.length && (dataList[0].hasOwnProperty('key')) && type === 'category-form-table')) {
                return false;
            }
            // 
            this.$emit('scrollUpdate');
            const _this = this;
            // 
            this.tabList = [];
            // 
            this.cacheData = {};
            // keykey
            dataList.sort((a, b) => parseInt(a.key) - parseInt(b.key));
            // 
            dataList.forEach((item) => {
                this.tabList.push({
                    [this.dTabName]: item[this.dTabName],
                    [this.dTabKey]: item[this.dTabKey],
                    [this.dTabNumberShow]: item[this.dTabNumberShow],
                    [this.dTabSize]: item[this.dTabSize],
                    [this.dTabIndex]: item[this.dTabIndex]
                });
                // 
                const _dataList = item.children.map((item) => {
                    // 
                    item.show = true;
                    // , !important, 
                    const _cDataList = item.data;
                    // 
                    //  
                    item.isTwoDimensionArray = _cDataList.length > 0 ? Array.isArray(_cDataList[0]) : false;
                    item.count = _cDataList.length;
                    return item;
                });
                item.children = _dataList;
                // 
                this.cacheData[item[this.dTabKey]] = item;
            });
            this.$nextTick(() => {
                const _query = _this.$route.query;
                let _queryExt = [];
                if (JSON.parse(decodeURIComponent(_query.nav)).length > 2) {
                    setTimeout(() => {
                        _queryExt = JSON.parse(decodeURIComponent(_query.nav))[2].ext;
                        _this.$refs.categoryCcTable.clickTabItem(_this.tabList[_queryExt.clickIndex]);
                    }, 0);
                }
                // if (_this.$route.query.clickIndex) {
                //  author lfan 2020-10-19
                // setTimeout(() => {
                //     _this.$refs.categoryCcTable.clickTabItem(_this.tabList[_queryExt.clickIndex]);
                // }, 0);
                // }
            });
        },
        // 
        setActiveItem(item) {
            // 
            this.activeItem = item;
        },
        //  
        selectTab(item) {
            if (item) {
                // 
                this.setActiveItem(item);
                // 
                this.setCurrentDataList(this.cacheData[item[this.dTabKey]][this.dTabChildren]);
            }
        },
        // 
        setCurrentDataList(dataList) {
            const _dataList = dataList.map((item) => {
                item.show = typeof (item.show) === 'boolean' ? item.show : true;
                // 
                item.categoryName = this.activeItem[this.dTabName];
                return item;
            });
            this.currentDataList = [];
            this.$nextTick(() => {
                //  @todo  json.parse()
                this.currentDataList = JSON.parse(JSON.stringify(_dataList));
            });
        },
        // table
        updateTableData(data) {
            // 
            this.$emit('scrollUpdate');
            this.total = data.total;
            this.currentDataList[this.clickIndex].offset = data.offset;
            // 
            this.currentDataList[this.clickIndex].data = data.data;
        },
        // 
        clickToggle(item) {
            item.show = !item.show;
            this.$forceUpdate();
        },
        // 
        pagingChange(page, item, index) {
            this.offset = page.offset;
            this.limit = page.limit;
            this.clickIndex = index;
            this.globalBus.$emit('global-query-search', {...page, index: this.activeItem[this.dTabIndex]});
        },
        // 
        bindGlobalEvent() {
            if (this.globalBus) {
                // 
                this.globalBus.$on('global-change-table-data', this.updateTableData);
            }
        },
        // 
        unbindGlobalEvent() {
            if (this.globalBus) {
                // table
                this.globalBus.$off('global-change-table-data', this.updateTableData);
            }
        }
    }
};
export default module;



